import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { LoginFormWrapper } from './style';
import { CenterLayout, TopHeader, PickAddressItem } from '../../components';
import { Address } from '../../services';
import { Link } from 'react-router-dom';
import { Modal, message, Empty, Skeleton } from 'antd';
const { confirm } = Modal;

const AddAddress = () => {
  return <Link to="/add-address">+ Tambah Alamat</Link>;
};

const Index = () => {
  const history = useHistory();

  const [address, setAddress] = useState([]);
  const [reFetch, setReFetch] = useState(false);
  const [isFetching, setIsFetching] = useState(false);
  useEffect(() => {
    const getAddress = async () => {
      setIsFetching(true);
      await Address.getAddress()
        .then(res => {
          setAddress(res.data);
        })
        .catch(() => {
          message.error('Something went wrong');
        })
        .finally(() => {
          setIsFetching(false);
        });
    };
    getAddress();
  }, [reFetch]);
  const addressRendered = address.map((item, i) => {
    const showDeleteConfirm = () => {
      confirm({
        title: 'Apakah anda ingin menghapus alamat ini ?',
        okText: 'Ya',
        okType: 'danger',
        cancelText: 'Tidak',
        onOk() {
          const deleteById = async () => {
            const res = await Address.deleteAddressById(item.id);
            if (res) {
              message.success('Deleted');
              setReFetch(!reFetch);
              return 1;
            }
            return 0;
          };
          deleteById();
        }
      });
    };
    const onEdit = () => {
      history.push(`/edit-address/${item.id}`);
    };
    return (
      <PickAddressItem
        text={item.address}
        key={i}
        id={item.id}
        isModify={true}
        onClickEdit={onEdit}
        onClickDelete={showDeleteConfirm}
      />
    );
  });
  return (
    <LoginFormWrapper>
      <TopHeader title={'Daftar Alamat'} topRightBtn={<AddAddress />} />
      <CenterLayout width={'400px'} className={'container_address_items'}>
        {isFetching ? (
          <Skeleton active />
        ) : address.length === 0 ? (
          <Empty />
        ) : (
          addressRendered
        )}
      </CenterLayout>
    </LoginFormWrapper>
  );
};

export default Index;
