import BaseService from '../baseServices';
import { URL } from '../../config/rest';

const getSubDistrict = () => {
  let parameters = {
    params: {
      page: 1,
      maxResult: 100
    }
  };
  return BaseService.get(URL.SUBDISTRICT, parameters);
};

const getSubDistrictByDistrict = id => {
  let parameters = {
    params: {
      page: 1,
      maxResult: 100,
      district_code: id
    }
  };
  return BaseService.get(URL.SUBDISTRICT, parameters);
};

export default {
  getSubDistrict,
  getSubDistrictByDistrict
};
