import React, { useState, useEffect } from 'react';
import { AddressFormWrapper } from './style';
import { useHistory } from 'react-router-dom';
import {
  CenterLayout,
  InputForm,
  InputSelect,
  Button,
  TopHeader,
  FormGroup
} from '../../components';
import { Form, Card, Input, message, Select, Spin } from 'antd';
import validate from 'validate.js';
import { ObjToArr } from '../../utils/converter';
import {
  Province,
  District,
  Subdistrict,
  Village,
  Address
} from '../../services';
const { TextArea } = Input;
const { Option } = Select;

const Index = () => {
  const history = useHistory();

  const [provinces, setProvinces] = useState([]);
  const [activeProvinces, setActiveProvinces] = useState();
  const [cities, setCities] = useState([]);
  const [activeCities, setActiveCities] = useState();
  const [isFetchingCities, setIsFetchingCities] = useState(false);
  const [subDistrict, setSubDistrict] = useState([]);
  const [activeSubDistrict, setActiveSubDistrict] = useState();
  const [isFetchingSubDistrict, setIsFetchingSubDistrict] = useState(false);
  const [villages, setVillages] = useState([]);
  const [activeVillages, setActiveVillages] = useState();
  const [isFetchingVillages, setIsFetchingVillages] = useState(false);
  const [zipCode, setZipCode] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [name, setName] = useState('');
  const [address, setAddress] = useState('');

  const constrains = {
    name: {
      presence: true
    },
    provinces: {
      presence: true
    },
    phoneNumber: {
      presence: true,
      length: {
        minimum: 11,
        maximum: 13
      },
      numericality: true
    },
    city: {
      presence: true
    },
    district: {
      presence: true
    },
    subDistrict: {
      presence: true
    },
    address: {
      presence: true
    },
    zipCode: {
      format: /\d{5}(-\d{4})?/
    }
  };
  const validatedData = {
    name: name,
    provinces: activeProvinces,
    city: activeCities,
    address: address,
    zipCode: zipCode,
    district: activeCities,
    subDistrict: activeSubDistrict,
    phoneNumber: phoneNumber
  };

  useEffect(() => {
    const getProvinces = async () => {
      await Province.getProvinces()
        .then(res => {
          setProvinces(res.data);
        })
        .catch(() => {
          message.error('Something went wrong');
        });
    };
    getProvinces();
  }, []);

  const getCities = async () => {
    setIsFetchingCities(true);
    await District.getDistrictByProvince(activeProvinces)
      .then(res => {
        setCities(res.data);
      })
      .catch(() => {
        message.error('Something went wrong');
      })
      .finally(() => {
        setIsFetchingCities(false);
      });
  };
  const getSubDistrict = async () => {
    setIsFetchingSubDistrict(true);
    await Subdistrict.getSubDistrictByDistrict(activeCities)
      .then(res => {
        setSubDistrict(res.data);
      })
      .catch(() => {
        message.error('Something went wrong');
      })
      .finally(() => {
        setIsFetchingSubDistrict(false);
      });
  };
  const getVillage = async () => {
    setIsFetchingVillages(true);
    await Village.getVillageBySubDistrict(activeSubDistrict)
      .then(res => {
        setVillages(res.data);
      })
      .catch(() => {
        message.error('Something went wrong');
      })
      .finally(() => {
        setIsFetchingVillages(false);
      });
  };
  const onSave = () => {
    const isNotValid = validate(validatedData, constrains);
    if (isNotValid) {
      ObjToArr(isNotValid).map(item => {
        message.error(item);
        return 0;
      });
    } else {
      const createAddress = async body => {
        await Address.createAddress(body)
          .then(res => {
            // console.log(res);
            message.success('Address Created');
            history.push('/list-address');
          })
          .catch(() => {
            message.error('Something went wrong');
          });
      };
      const body = {
        name: name,
        address: address,
        province: activeProvinces,
        district: activeCities,
        subdistrict: activeSubDistrict,
        village: activeVillages,
        phone: phoneNumber,
        type: 'Kantor'
      };
      createAddress(body);
    }
  };
  const provincesRendered = provinces.map((item, i) => {
    return (
      <Option key={i} value={item.code}>
        {item.name}
      </Option>
    );
  });
  const citiesRendered = cities.map((item, i) => {
    return (
      <Option key={i} value={item.code}>
        {item.name}
      </Option>
    );
  });
  const subDistrictRendered = subDistrict.map((item, i) => {
    return (
      <Option key={i} value={item.code}>
        {item.name}
      </Option>
    );
  });
  const villagesRendered = villages.map((item, i) => {
    return (
      <Option key={i} value={item.code}>
        {item.name}
      </Option>
    );
  });
  const onBack = () => {
    history.push(`/list-address`);
  };
  return (
    <AddressFormWrapper>
      <TopHeader title={'Tambah Alamat Baru'} onBack={onBack} />
      <CenterLayout width={'400px'} className={'container_register_form'}>
        <Card bordered={false} className={'container_form_register'}>
          <Form>
            <FormGroup title={'Provinsi'}>
              <InputSelect
                placeholder={'Cari Provinsi'}
                value={activeProvinces}
                onChange={val => setActiveProvinces(val)}
              >
                {provincesRendered}
              </InputSelect>
            </FormGroup>
            <FormGroup title={'Kota'}>
              <InputSelect
                placeholder={'Cari Kota'}
                value={activeCities}
                onFocus={getCities}
                onChange={val => setActiveCities(val)}
                notFoundContent={
                  isFetchingCities ? <Spin size="small" /> : null
                }
                disabled={activeProvinces === undefined}
              >
                {citiesRendered}
              </InputSelect>
            </FormGroup>
            <FormGroup title={'Kecamatan'}>
              <InputSelect
                placeholder={'Cari Kecamatan'}
                value={activeSubDistrict}
                onFocus={getSubDistrict}
                onChange={val => setActiveSubDistrict(val)}
                notFoundContent={
                  isFetchingSubDistrict ? <Spin size="small" /> : null
                }
                disabled={activeCities === undefined}
              >
                {subDistrictRendered}
              </InputSelect>
            </FormGroup>
            <FormGroup title={'Desa'}>
              <InputSelect
                placeholder={'Cari Desa'}
                value={activeVillages}
                onFocus={getVillage}
                onChange={val => setActiveVillages(val)}
                notFoundContent={
                  isFetchingVillages ? <Spin size="small" /> : null
                }
                disabled={activeSubDistrict === undefined}
              >
                {villagesRendered}
              </InputSelect>
            </FormGroup>
            <FormGroup title={'Nama'}>
              <InputForm
                placeholder={'Masukkan Nama'}
                value={name}
                onChange={e => setName(e.target.value)}
              />
            </FormGroup>
            <FormGroup title={'Kode Pos'}>
              <InputForm
                placeholder={'Masukkan Kode Pos'}
                value={zipCode}
                onChange={e => setZipCode(e.target.value)}
              />
            </FormGroup>
            <FormGroup title={'No. HP'}>
              <InputForm
                placeholder={'Masukkan No. HP'}
                value={phoneNumber}
                onChange={e => setPhoneNumber(e.target.value)}
              />
            </FormGroup>
            <FormGroup title={'Alamat'}>
              <TextArea
                placeholder="Alamat"
                autoSize={{ minRows: 4, maxRows: 8 }}
                value={address}
                onChange={e => setAddress(e.target.value)}
              />
            </FormGroup>
            <Button
              text={'Tambahkan Alamat'}
              className={'container_save_button'}
              onClick={onSave}
            />
          </Form>
        </Card>
      </CenterLayout>
    </AddressFormWrapper>
  );
};

export default Index;
