import React from 'react';
import { useHistory } from 'react-router';
import { DropDownResult, DropDownResultItem } from '../../components';
import { Input, Icon, message, Empty, Skeleton } from 'antd';
import { Product, File } from '../../services';

function SearchInput({
  search,
  setSearch,
  setSearchResultDisplay,
  params,
  setIsFetching,
  setSearchResult,
  setSearchResultImage
}) {
  const history = useHistory();
  const UpdateInput = e => {
    if (e.target.value !== '') {
      setSearchResultDisplay(true);
      setIsFetching(true);
      if (params.district_code !== undefined) {
        Product.getProductByNameAndDistrict(
          e.target.value,
          params.district_code
        )
          .then(res => {
            setSearchResult(res.data);
            res.data.map(item => {
              const bucketName = item.bucket_name;
              const objectName = item.object_name;
              const body = {
                bucketName: bucketName,
                objectName: objectName
              };
              const downloadUrl = async () => {
                try {
                  const res = await File.downloadReq(body);
                  setSearchResultImage(prevState =>
                    prevState.concat(res.data.url)
                  );
                } catch (error) {
                  item.image = '';
                }
              };
              downloadUrl(body);
              return 0;
            });
          })
          .catch(() => {
            message.error('something went wrong');
          })
          .finally(() => {
            setIsFetching(false);
          });
      } else {
        Product.getProductByKeyword(e.target.value)
          .then(res => {
            setSearchResult(res.data);
            res.data.map(item => {
              const bucketName = item.bucket_name;
              const objectName = item.object_name;
              const body = {
                bucketName: bucketName,
                objectName: objectName
              };
              const downloadUrl = async () => {
                try {
                  const res = await File.downloadReq(body);
                  setSearchResultImage(prevState =>
                    prevState.concat(res.data.url)
                  );
                } catch (error) {
                  item.image = '';
                }
              };
              downloadUrl(body);
              return 0;
            });
          })
          .catch(() => {
            message.error('something went wrong');
          })
          .finally(() => {
            setIsFetching(false);
          });
      }
    } else {
      setSearchResultDisplay(false);
    }
    setSearch(e.target.value);
  };
  const onEnter = e => {
    const location = { pathname: '/search', search: `?query=${search}` };
    if (e.key === 'Enter') {
      history.push(location);
    }
  };
  return (
    <Input
      placeholder={'Search...'}
      prefix={<Icon type="search" />}
      className={'component_search_input'}
      onKeyDown={onEnter}
      onChange={UpdateInput}
      value={search}
      name="search"
    />
  );
}

export default function SearchWrapper(props) {
  const {
    search,
    setSearch,
    setSearchResultDisplay,
    searchResultDisplay,
    searchResult,
    params,
    isFetching,
    setIsFetching,
    setSearchResult,
    setSearchResultImage,
    searchResultImage,
    history
  } = props;
  const searchResultRendered = searchResult.map((item, i) => {
    const onDetail = () => {
      history.push(`/product-detail/${item.id}`);
    };
    return (
      <DropDownResultItem
        text={item.name}
        desc={item.shortDescription}
        img={searchResultImage[i]}
        key={i}
        onClick={onDetail}
      />
    );
  });
  return (
    <div>
      <SearchInput
        search={search}
        setSearch={setSearch}
        setSearchResultDisplay={setSearchResultDisplay}
        params={params}
        isFetching={isFetching}
        setIsFetching={setIsFetching}
        setSearchResult={setSearchResult}
        searchResultImage={searchResultImage}
        setSearchResultImage={setSearchResultImage}
      />
      <DropDownResult
        text={search}
        display={searchResultDisplay}
        linkTo={{
          pathname: '/search',
          search: `?query=${search}`
        }}
      >
        {searchResult.length === 0 ? (
          <Empty />
        ) : isFetching ? (
          <Skeleton active />
        ) : (
          searchResultRendered
        )}
      </DropDownResult>
    </div>
  );
}
