import React, { useState } from 'react';
import { useHistory } from 'react-router';
import useAsyncEffect from 'use-async-effect';
import { CenterLayout, SetLocation } from '../../components';
import { HeaderMenuWrapper } from './style';
import { Icon, Col, Row, Affix, message, Select } from 'antd';
import { getCookie, setCookie } from '../../utils/cookies';
import { District, Notification } from '../../services';
import { LeftSideBar } from '../';
import { ShoppingCart } from '../';
import SearchWrapper from './search.container';
import DropDownMenu from './dropDownMenu.container';
const { Option } = Select;

const Index = props => {
  const { params } = props;
  const history = useHistory();
  const [cities, setCities] = useState([]);
  const [search, setSearch] = useState(
    props.searchQuery === undefined ? '' : props.searchQuery
  );
  const [activeCity, setActiveCity] = useState(
    getCookie('location') === '' ? '' : getCookie('location')
  );
  const [searchResultDisplay, setSearchResultDisplay] = useState(false);
  const [searchResult, setSearchResult] = useState([]);
  const [searchResultImage, setSearchResultImage] = useState([]);
  const [leftMenusActive, setLeftMenusActive] = useState('0');
  const [showLeftSideBar, setShowLeftSideBar] = useState(false);
  const [showSetLocations, setShowSetLocations] = useState(
    getCookie('location') === '' && props.isRoot
  );
  const [isFetchingSearch, setIsFetchingSearch] = useState(false);
  const [notifications, setNotifications] = useState([]);
  const [showNotifications, setShowNotifications] = useState(false);
  const [selectedDistrict, setSelectedDistrict] = useState('');
  const [isReFetch] = useState(false);
  const [isScrolled, setIsScrolled] = useState(false);
  useAsyncEffect(
    async isMounted => {
      const supportedDistrict = await District.getSupportedDistrict()
        .then(res => {
          const filteredCity = res.data.filter(
            item => item.code === activeCity
          )[0];
          if (filteredCity) {
            setSelectedDistrict(filteredCity.name);
          }
          return res.data;
        })
        .catch(() => {
          message.error('Something went wrong');
          return [];
        });
      const notificationResponse = await Notification.getNotification()
        .then(res => res.data.filter(item => item.type === 2))
        .catch(() => {
          message.error('Something went wrong');
          return [];
        });
      if (!isMounted()) return;
      setCities(supportedDistrict);
      setNotifications(notificationResponse);
    },
    [isReFetch, activeCity]
  );
  function changeLeftMenusActive(x) {
    setLeftMenusActive(x.key);
  }
  function onWelcome() {
    setCookie('location', params.district_code);
    let location = {
      search: `?district_code=${activeCity}`
    };
    history.push(location);
    setShowSetLocations(false);
    setActiveCity(params.district_code);
  }
  const optionRendered = cities.map((item, i) => {
    return (
      <Option value={item.code} key={i}>
        {item.name}
      </Option>
    );
  });
  const onChangeSetLocation = value => {
    setActiveCity(value);
  };
  return (
    <div>
      <SetLocation
        show={showSetLocations}
        options={cities}
        value={activeCity}
        onNext={onWelcome}
        onSelect={onChangeSetLocation}
      >
        {optionRendered}
      </SetLocation>
      <LeftSideBar
        show={showLeftSideBar}
        activeMenu={[leftMenusActive]}
        onHide={setShowLeftSideBar}
        onChangeActiveMenu={changeLeftMenusActive}
      />
      <Affix onChange={affixed => setIsScrolled(affixed)}>
        <HeaderMenuWrapper>
          <CenterLayout>
            <Row
              type="flex"
              justify="space-between"
              align="middle"
              className={'component_row_wrapper'}
            >
              <Col>
                {props.isRoot ? (
                  <span
                    onClick={() => setShowLeftSideBar(true)}
                    className="container_menu_left"
                  >
                    <Icon type="menu" style={{ fontSize: '24px' }} />
                  </span>
                ) : (
                  <span className="container_menu_left" onClick={props.onBack}>
                    <Icon type="arrow-left" style={{ fontSize: '24px' }} />
                  </span>
                )}
              </Col>
              <Col
                xs={12}
                sm={14}
                lg={18}
                className={'component_search_input_wrapper'}
              >
                <SearchWrapper
                  search={search}
                  setSearch={setSearch}
                  setSearchResultDisplay={setSearchResultDisplay}
                  searchResultDisplay={searchResultDisplay}
                  searchResult={searchResult}
                  params={params}
                  isFetching={isFetchingSearch}
                  setIsFetching={setIsFetchingSearch}
                  setSearchResult={setSearchResult}
                  searchResultImage={searchResultImage}
                  setSearchResultImage={setSearchResultImage}
                  history={history}
                />
              </Col>
              <Col md={4}>
                <DropDownMenu
                  cities={cities}
                  activeCity={activeCity}
                  setActiveCity={setActiveCity}
                  notifications={notifications}
                  showNotifications={showNotifications}
                  setShowNotifications={setShowNotifications}
                  params={params}
                  selectedDistrict={selectedDistrict}
                  setSelectedDistrict={setSelectedDistrict}
                />
              </Col>
            </Row>
          </CenterLayout>
        </HeaderMenuWrapper>
      </Affix>
      <ShoppingCart isScrolled={isScrolled} />
    </div>
  );
};

export default Index;
