import BaseService from '../baseServices';
import { URL } from '../../config/rest';

const getAddress = () => {
  let parameters = {
    params: {
      page: 1,
      maxResult: 100
    }
  };
  return BaseService.get(URL.ADDRESS, parameters);
};

const deleteAddressById = id => {
  return BaseService.delete(URL.ADDRESS_BY_ID(id));
};

const createAddress = body => {
  return BaseService.post(URL.ADDRESS, body);
};

const getAddressById = id => {
  return BaseService.get(URL.ADDRESS_BY_ID(id));
};

const updateAddressById = (id, body) => {
  return BaseService.put(URL.ADDRESS_BY_ID(id), body);
};

export default {
  getAddress,
  deleteAddressById,
  createAddress,
  getAddressById,
  updateAddressById
};
