import React, { useState } from 'react';
import { LoginFormWrapper } from './style';
import {
  CenterLayout,
  InputForm,
  InputPassword,
  Button,
  TopHeader
} from '../../components';
import { Form, Card, message } from 'antd';
import validate from 'validate.js';
import { ObjToArr } from '../../utils/converter';
import { Auth } from '../../services';
import { Link } from 'react-router-dom';

const LoginForm = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const constrains = {
    nama: {
      presence: true,
      length: {
        minimum: 1
      }
    },
    nomorHp: {
      presence: true,
      length: {
        minimum: 11,
        maximum: 13
      },
      numericality: true
    },
    email: {
      email: true
    },
    password: {
      presence: true,
      length: {
        minimum: 6
      }
    }
  };
  const validatedData = {
    nama: name,
    nomorHp: username,
    email: email,
    password: password
  };
  const onSave = () => {
    const isNotValid = validate(validatedData, constrains);
    if (isNotValid) {
      ObjToArr(isNotValid).map(item => {
        message.error(item);
        return 0;
      });
    } else {
      const register = async body => {
        const res = await Auth.register(body);
        if (res) {
          return alert('Register Successfully');
        }
        return alert(res.message);
      };
      const body = {
        email: email,
        name: name,
        password: password
      };
      register(body);
    }
  };
  return (
    <LoginFormWrapper>
      <TopHeader title={'Register'} />
      <CenterLayout width={'400px'} className={'container_register_form'}>
        <Card bordered={false} className={'container_form_register'}>
          <Form>
            <InputForm
              placeholder={'No Hp'}
              value={username}
              onChange={e => setUsername(e.target.value)}
            />
            <InputPassword
              placeholder={'Passowrd'}
              value={password}
              onChange={e => setPassword(e.target.value)}
            />
            <InputForm
              placeholder={'Nama'}
              value={name}
              onChange={e => setName(e.target.value)}
            />
            <InputForm
              placeholder={'Email'}
              value={email}
              type={'email'}
              onChange={e => setEmail(e.target.value)}
            />
            <Button text={'Daftar'} onClick={() => onSave()} />
          </Form>
        </Card>
        <Card bordered={false}>
          Sudah Punya Akun ?
          <Link to={'/login'}>
            <span className="container_form_register_button">Login</span>
          </Link>
        </Card>
      </CenterLayout>
    </LoginFormWrapper>
  );
};

export default LoginForm;
