import React from 'react';
import image from '../../assets/img';
import { CardWrapper } from './style';
import Button from '../button';
import { Icon } from 'antd';
import { Link } from 'react-router-dom';

export default function Card({
  src = image.mango,
  title,
  desc,
  discountPrice,
  mainPrice,
  perItem,
  minPurchase,
  btnText = 'Beli',
  label = '',
  showModifyItem,
  count = 0,
  onBuy,
  unit,
  onAdd,
  onMin,
  toProductDetail = ''
}) {
  return (
    <CardWrapper className="component_card" img={src}>
      <Link to={toProductDetail}>
        <div className="component_card_image">
          {showModifyItem && (
            <div className="component_card_added_wrapper">
              <div className="component_card_added_count">{count}</div>
              <div className="component_card_added_desc">Added</div>
            </div>
          )}
          <span className="component_card_label">
            <span>{label}</span>
          </span>
        </div>
      </Link>
      <div className="component_card_wrapper">
        <div>
          <h3 className="component_card_title">
            <Link to={toProductDetail}>{title}</Link>
          </h3>
          <h4 className="component_card_desc">{desc}...</h4>
        </div>
        <div className="component_card_price">
          <div className="component_card_price_discount">{discountPrice}</div>
          <div>
            <span className="component_card_main_price">{mainPrice}</span>
            <span className="component_card_per_item">
              / {`${perItem} ${unit}`}
            </span>
          </div>
          <div className="component_card_min_purchase">
            min {`${minPurchase} ${unit}`}
          </div>
        </div>
        {showModifyItem ? (
          <div className="component_card_icon_wrapper">
            <span className="component_card_icon" onClick={onMin}>
              <Icon type="minus" />
            </span>
            <span className="component_card_icon" onClick={onAdd}>
              <Icon type="plus" />
            </span>
          </div>
        ) : (
          <Button
            text={btnText}
            btnClassName={'component_card_buy_button'}
            onClick={onBuy}
          />
        )}
      </div>
    </CardWrapper>
  );
}
