import React, { useState, useEffect } from 'react';
import Header from './header';
import { ListTransactionWrapper } from './style.js';
import { Menu, message } from 'antd';
import { CenterLayout } from '../../components';
import WaitingList from './waiting.container';
import OnProgressList from './onProgress.container';
import FinishList from './finish.container';
import { Transaction } from '../../services';

function Index() {
  const [activeMenu, setActiveMenu] = useState('0');
  const [waiting, setWaiting] = useState([]);
  const [onProgress, setOnProgress] = useState([]);
  const [finished, setFinfished] = useState([]);
  const [isFetching, setIsFetching] = useState(false);
  useEffect(() => {
    const getTransaction = async () => {
      setIsFetching(true);
      await Transaction.getTransaction()
        .then(res => {
          const waitingResponse = res.data.filter(
            item => item.transaction_status === 0
          );
          const onProgressResponse = res.data.filter(
            item => item.transaction_status === 2
          );
          const finishedResponse = res.data.filter(
            item => item.transaction_status === 3
          );
          setWaiting(waitingResponse);
          setOnProgress(onProgressResponse);
          setFinfished(finishedResponse);
        })
        .catch(() => {
          message.error('Something went wrong');
        })
        .finally(() => {
          setIsFetching(false);
        });
    };
    getTransaction();
  }, [activeMenu]);
  return (
    <ListTransactionWrapper>
      <Header />
      <CenterLayout>
        <Menu
          className="container_tabs_menu"
          mode="horizontal"
          defaultSelectedKeys={activeMenu}
          onSelect={x => {
            setActiveMenu(x.key);
          }}
        >
          <Menu.Item key="0">Menunggu Pembayaran</Menu.Item>
          <Menu.Item key="1">Dalam Proses</Menu.Item>
          <Menu.Item key="2">Selesai</Menu.Item>
        </Menu>
      </CenterLayout>
      <div className="container_list_transaction_content_wrapper">
        <div className="container_list_transaction_content_content">
          <CenterLayout>
            <div>
              {(activeMenu === '0' && (
                <WaitingList isFetching={isFetching} itemList={waiting} />
              )) ||
                (activeMenu === '1' && (
                  <OnProgressList
                    isFetching={isFetching}
                    itemList={onProgress}
                  />
                )) ||
                (activeMenu === '2' && (
                  <FinishList isFetching={isFetching} itemList={finished} />
                ))}
            </div>
          </CenterLayout>
        </div>
      </div>
    </ListTransactionWrapper>
  );
}

export default Index;
