import BaseService from '../baseServices';
import { URL } from '../../config/rest';

const forgotPassword = body => {
  return BaseService.get(URL.REQUEST_RESET_PASSWORD, body);
};

const register = body => {
  return BaseService.post(URL.REGISTER, body);
};

const resetPassword = body => {
  return BaseService.post(URL.RESET_PASSWORD, body);
};

export default {
  forgotPassword,
  register,
  resetPassword
};
