import BaseService from '../baseServices';
import { URL } from '../../config/rest';

const getProduct = () => {
  let parameters = {
    params: {
      page: 1,
      maxResult: 10
    }
  };
  return BaseService.get(URL.PRODUCT, parameters);
};

const getProductByDistrict = districtCode => {
  let parameters = {
    params: {
      page: 1,
      maxResult: 10,
      district_code: districtCode
    }
  };
  return BaseService.get(URL.PRODUCT, parameters);
};

const getProductByNameAndDistrict = (keyword, districtCode, maxResult = 10) => {
  let parameters = {
    params: {
      page: 1,
      maxResult: maxResult,
      district_code: districtCode,
      name: keyword
    }
  };
  return BaseService.get(URL.PRODUCT, parameters);
};

const getProductByKeyword = (keyword, maxResult = 10) => {
  let parameters = {
    params: {
      page: 1,
      maxResult: maxResult,
      name: keyword
    }
  };
  return BaseService.get(URL.PRODUCT, parameters);
};

const getProductByCategory = categoryId => {
  let parameters = {
    params: {
      page: 1,
      maxResult: 10,
      category_id: categoryId
    }
  };
  return BaseService.get(URL.PRODUCT, parameters);
};

const getProductById = id => {
  return BaseService.get(URL.PRODUCT_BY_ID(id));
};

export default {
  getProduct,
  getProductByDistrict,
  getProductById,
  getProductByCategory,
  getProductByNameAndDistrict,
  getProductByKeyword
};
