import React from 'react';
import { DetailProductWrapper } from './style';
import { Button } from '../';
import { ToPrice, CheckInteger } from '../../utils/converter';
import { Icon, Input } from 'antd';
import { setCookie, getCookie } from '../../utils/cookies';

export default function CategoryCard(props) {
  const onAdd = () => {
    // let items = JSON.parse(getCookie('items'))
    props.onCount(props.count + 1);
    // let res = items.find(({ id }) => id === props.id)
    // if (res === undefined) {
    // 	let item = {
    // 		id: props.id,
    // 		price: props.mainPrice,
    // 		total: props.count + 1
    // 	}
    // 	items.push(item)
    // } else {
    // 	res.total = props.count + 1
    // }
    // setCookie('items',JSON.stringify(items))
  };
  const onMin = () => {
    if (props.count === 0) {
      props.onShowModifyItem(false);
    } else {
      // let items = JSON.parse(getCookie('items'))
      props.onCount(props.count - 1);
      // let res = items.find(({ id }) => id === props.id)
      // if (res === undefined) {
      // 	let item = {
      // 		id: props.id,
      // 		price: props.mainPrice,
      // 		total: props.count - 1
      // 	}
      // 	items.push(item)
      // } else {
      // 	res.total = props.count - 1
      // }
      // setCookie('items',JSON.stringify(items))
    }
  };
  const onInput = e => {
    props.onCount(CheckInteger(e.target.value));
    let items = JSON.parse(getCookie('items'));
    let res = items.find(({ id }) => id === props.id);
    if (res === undefined) {
      let item = {
        id: props.id,
        price: props.mainPrice,
        total: e.target.value
      };
      items.push(item);
    } else {
      res.total = e.target.value;
    }
    setCookie('items', JSON.stringify(items));
  };
  return (
    <DetailProductWrapper>
      <div className="component_detail_product_image">
        <div className="component_detail_product_image_wrapper">
          <img src={props.imgProduct} alt="" />
        </div>
        <div className="component_detail_product_image_label">
          <span>{props.label}</span>
        </div>
        {props.showModifyItem ? (
          <div className="component_detail_product_icon">
            <span onClick={onMin}>
              <Icon type="minus" />
            </span>
            <Input value={props.count} onChange={onInput} />
            <span onClick={onAdd}>
              <Icon type="plus" />
            </span>
          </div>
        ) : (
          <Button
            text={'Beli'}
            className={'component_detail_product_buy_wrapper'}
            btnClassName={'component_detail_product_buy_button'}
            onClick={props.onShowModifyItem}
          />
        )}
      </div>
      <div className="component_detail_product_text">
        <div className="component_detail_product_title">{props.title}</div>
        <div className="component_detail_product_sub_title">
          {props.subTitle}
        </div>
        <div className="component_detail_product_discPrice">
          {ToPrice(props.discPrice)}
        </div>
        <div>
          <span className="component_detail_product_mainPrice">
            {ToPrice(props.mainPrice)}
          </span>
          <span className="component_detail_product_per_item">
            / {props.perItem}
          </span>
          <span className="component_detail_product_available">
            Tersedia {props.available}
          </span>
        </div>
        <div className="component_detailProduct_desc">{props.desc}</div>
      </div>
    </DetailProductWrapper>
  );
}
