import React from 'react';
import { CategoryCardWrapper } from './style';

export default function CategoryCard(props) {
  return (
    <CategoryCardWrapper className="component_category_card">
      <div
        className="component_category_card_wrapper"
        style={{ backgroundImage: `url(${props.src})` }}
        onClick={props.onClick}
      >
        <span className="component_category_card_text">
          {props.special && <hr />}
          {props.text}
        </span>
        <span
          className={
            props.special
              ? 'component_category_card_bg_linier_special'
              : 'component_category_card_bg_linier'
          }
        />
      </div>
      {/*<img src={ props.src } alt={ props.alt } />*/}
    </CategoryCardWrapper>
  );
}
