import React from 'react';
import {
  Carousel,
  HeaderMenu,
  Products,
  ContentWrapper,
  CategoriesItems
} from '../../containers';
import queryString from 'query-string';

function Index(props) {
  const params = queryString.parse(props.location.search);
  return (
    <div>
      <HeaderMenu isRoot={true} params={params} />
      <ContentWrapper>
        <Carousel />
        <CategoriesItems />
        <Products params={params} />
      </ContentWrapper>
    </div>
  );
}

export default Index;
