import React, { useState } from 'react';
import {
  ShoppingCart,
  ShoppingCartBottom,
  ShoppingCartItem
} from '../../components';
import { Affix, Skeleton, Empty, Modal, message } from 'antd';
import { ToPrice } from '../../utils/converter';
import { File, Cart } from '../../services';
import { useHistory } from 'react-router-dom';
import useAsyncEffect from 'use-async-effect';
const { confirm } = Modal;

const Index = props => {
  const history = useHistory();

  const [displayCart, setDisplayCart] = useState(false);
  const [cartList, setCartList] = useState([]);
  const [isFetchingCart, setIsFetchingCart] = useState(false);
  const [cartListImages, setCartListImages] = useState([]);
  const [reFetch, setReFetch] = useState(false);
  const [isDisabledModifyItem, setIsDisabledModifyItem] = useState(false);

  useAsyncEffect(
    async isMounted => {
      const getCarts = async () => {
        setIsFetchingCart(true);
        setIsDisabledModifyItem(true);
        await Cart.getCart()
          .then(res => {
            setCartList(res.data);
            res.data.map(item => {
              const bucketName = item.product.bucket_name;
              const objectName = item.product.object_name;
              const body = {
                bucketName: bucketName,
                objectName: objectName
              };
              const downloadFile = async () => {
                await File.downloadReq(body)
                  .then(res => {
                    setCartListImages(prevState =>
                      prevState.concat(res.data.url)
                    );
                  })
                  .catch(() => {
                    message.error('something went wrong');
                  });
              };
              downloadFile();
              return 0;
            });
          })
          .catch(() => {
            message.error('Something went wrong');
          })
          .finally(() => {
            setIsFetchingCart(false);
            setIsDisabledModifyItem(false);
          });
      };
      if (!isMounted()) return;
      getCarts();
    },
    [reFetch]
  );

  const cartItemsRendered = cartList.map((item, i) => {
    const updateCartItem = async count => {
      setIsDisabledModifyItem(true);
      await Cart.updateCartItemByProductId(item.id, count)
        .then(() => 1)
        .catch(() => message.error('Something went wrong'))
        .finally(() => {
          setIsDisabledModifyItem(false);
        });
    };
    const showDeleteConfirm = () => {
      confirm({
        title: 'Apakah anda ingin menghapus barang ini ?',
        okText: 'Ya',
        okType: 'danger',
        cancelText: 'Tidak',
        onOk() {
          const deleteById = async id => {
            await Cart.deleteCart(id)
              .then(() => {
                message.success('Produk Berhasil Dihapus');
              })
              .catch(() => message.error('something went wrong'))
              .finally(() => {
                setReFetch(!reFetch);
              });
          };
          deleteById(item.id);
        }
      });
    };
    return (
      <ShoppingCartItem
        key={i}
        img={cartListImages[i]}
        title={item.product.name}
        desc={item.product.shortDescription}
        price={item.price !== undefined ? ToPrice(item.price.amount) : 0}
        count={item.qty}
        disabled={isDisabledModifyItem}
        onDelete={showDeleteConfirm}
        onMin={() => updateCartItem(item.qty === 0 ? 0 : item.qty - 1)}
        onAdd={() => updateCartItem(item.qty + 1)}
      />
    );
  });
  return (
    <div>
      <Affix offsetBottom={100}>
        <div>
          <ShoppingCartBottom
            totalPrice={1000}
            onNext={() => setDisplayCart(true)}
            isScrolled={props.isNotRoot ? true : props.isScrolled}
            desc={'Free ongkir pembelian di atas Rp. 100.000'}
            count={cartList.length}
          />
        </div>
      </Affix>
      <ShoppingCart
        show={displayCart}
        onHide={setDisplayCart}
        count={cartList.length}
        onCheckout={() => history.push('/checkout')}
      >
        {isFetchingCart ? (
          <Skeleton active />
        ) : cartList.length === 0 ? (
          <Empty />
        ) : (
          cartItemsRendered
        )}
      </ShoppingCart>
    </div>
  );
};

export default Index;
