import React from 'react';
import { ShoppingCartWrapper } from './style';
import { ShoppingCartBottomWrapper } from './shoppingCartBottom.style';
import { Icon, Input, Row } from 'antd';
// import { CheckInteger } from '../../utils/converter';
import { Button, CenterLayout } from '../';
import { ToPrice } from '../../utils/converter';

const CartLeft = ({ count = 0, price = 0, desc }) => {
  return (
    <ShoppingCartBottomWrapper>
      <div className="component_cart_left">
        <div className="component_cart_icon_wrapper">
          <span>{count}</span>
          <div className="component_cart_icon">
            <Icon type="shopping-cart" style={{ fontSize: '24px' }} />
          </div>
        </div>
        <div className="component_cart_icon_text">
          <div className="component_cart_icon_price">{ToPrice(price)}</div>
          <div className="component_cart_icon_desc">{desc}</div>
        </div>
      </div>
    </ShoppingCartBottomWrapper>
  );
};

const ShoppingCartBottom = props => {
  return (
    <ShoppingCartBottomWrapper isScrolled={props.isScrolled}>
      <div className={'component_cart_bottom'}>
        <CenterLayout>
          <Row
            type="flex"
            justify="space-between"
            className="component_cart_bottom_wrapper"
          >
            <CartLeft
              desc={props.desc}
              price={props.totalPrice}
              count={props.count}
            />
            <Button
              text={'Lanjut'}
              btnClassName={'component_cart_next_button'}
              onClick={props.onNext}
            />
          </Row>
        </CenterLayout>
      </div>
    </ShoppingCartBottomWrapper>
  );
};

const ShoppingCartTitle = props => {
  return (
    <div className="component_shopping_cart_top">
      <span onClick={() => props.onHide(false)}>
        <Icon
          type="arrow-left"
          className={'component_shopping_cart_title_back'}
        />
      </span>
      <span className={'component_shopping_cart_title'}>Keranjang Belanja</span>
    </div>
  );
};

const ShoppingCartCheckout = props => {
  return (
    <div className="component_shopping_cart_checkout">
      <div className="component_shopping_cart_checkout_content_wrapper">
        <div>
          <div className="component_cart_icon_wrapper">
            <span>{props.count}</span>
            <div className="component_cart_icon">
              <Icon type="shopping-cart" style={{ fontSize: '24px' }} />
            </div>
          </div>
          <div className="component_shopping_cart_checkout_price">
            {ToPrice(props.price)}
          </div>
        </div>
        <Button
          text={'Check Out'}
          className={'component_cart_checkout_button'}
          onClick={props.onCheckout}
        />
      </div>
    </div>
  );
};

const ShoppingCartItem = props => {
  return (
    <div className="component_shopping_cart_list_item">
      <div className="component_shopping_cart_list_item_top">
        <div
          className="component_shopping_cart_list_item_img"
          style={{ backgroundImage: `url('${props.img}')` }}
        />
        <div className="component_shopping_cart_list_item_desc">
          <div className="component_shopping_cart_list_item_desc_wrapper">
            <div className="component_shopping_cart_list_item_title">
              {props.title}
            </div>
            <div className="component_shopping_cart_list_item_sub_title">
              {props.desc}
              <br />
              {props.price}
            </div>
          </div>
          <div
            className="component_shopping_cart_delete_icon"
            onClick={props.onDelete}
          >
            <Icon type="delete" style={{ fontSize: '18px' }} />
          </div>
        </div>
      </div>
      <div className="component_shopping_cart_list_item_bottom">
        <div className="component_shopping_cart_count">
          {props.disabled ? (
            <span>
              <Icon
                type="minus"
                className="component_shopping_cart_count_icon disabled"
              />
            </span>
          ) : (
            <span onClick={props.onMin}>
              <Icon
                type="minus"
                className="component_shopping_cart_count_icon"
              />
            </span>
          )}
          <Input
            value={props.count}
            size="small"
            onChange={props.onChange}
            disabled={props.disabled}
          />
          {props.disabled ? (
            <span>
              <Icon
                type="plus"
                className="component_shopping_cart_count_icon disabled"
              />
            </span>
          ) : (
            <span onClick={props.onAdd}>
              <Icon
                type="plus"
                className="component_shopping_cart_count_icon"
              />
            </span>
          )}
        </div>
        <div className="component_shopping_cart_price">{props.price}</div>
      </div>
    </div>
  );
};

const ShoppingCart = props => {
  return (
    <ShoppingCartWrapper show={props.show}>
      <div
        className="ant-modal-mask component_shopping_cart_modal_mask"
        onClick={() => props.onHide(false)}
      >
        <div
          className="component_shopping_cart_modal"
          onClick={e => {
            e.stopPropagation();
          }}
        >
          <ShoppingCartTitle onHide={props.onHide} />
          <div className="component_shopping_cart_item_wrapper">
            {props.children}
          </div>
          <ShoppingCartCheckout
            count={props.count}
            onCheckout={props.onCheckout}
          />
        </div>
      </div>
    </ShoppingCartWrapper>
  );
};

export { ShoppingCart, ShoppingCartItem, ShoppingCartBottom };
