import React from 'react';
import { PaymentMethodOptionWrapper } from './paymentMethodOption.style'
import { PaymentMethodBoxWrapper } from './paymentMethodBox.style'
import { Icon } from 'antd'

const PaymentMethodOption = (props) => {
	return(
		<PaymentMethodOptionWrapper isSelected={ props.isSelected }
									onClick={ props.onClick }>
			<div className="component_payment_method_option">
				<img src={ props.img } alt={ props.name }/>
				<span className="component_payment_method_name">{ props.name }</span>
			</div>
			{
				props.isSelected &&
				<div className="component_payment_method_checked">
					<Icon type="check" />
				</div>
			}
		</PaymentMethodOptionWrapper>
	)
}

const PaymentMethodBox = (props) => {
	return(
		<PaymentMethodBoxWrapper isSelected={ props.isSelected }
								 onClick={ props.onClick } >
			<span className="component_method_box_text">
				{ 
					props.text === undefined ?
					<img src={ props.img } alt="Alt"/>
					:
					props.text 
				}
			</span>
		</PaymentMethodBoxWrapper>
	)
}

export {
	PaymentMethodOption,
	PaymentMethodBox
}