import React, { useState } from 'react';
import { LoginFormWrapper } from './style';
import {
  CenterLayout,
  InputPassword,
  Button,
  TopHeader
} from '../../components';
import { Form, Card, message } from 'antd';
import validate from 'validate.js';
import { ObjToArr } from '../../utils/converter';
import { Auth } from '../../services';
import { getCookie } from '../../utils/cookies';

const Index = () => {
  const [oldPassword, setOldPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmNewPassword, setConfirmNewPassword] = useState('');
  const constrains = {
    oldPassword: {
      presence: true,
      length: {
        minimum: 6
      }
    },
    newPassword: {
      presence: true,
      length: {
        minimum: 6
      }
    },
    confirmNewPassword: {
      presence: true,
      length: {
        minimum: 6
      },
      equality: 'newPassword'
    }
  };
  const validatedData = {
    oldPassword: oldPassword,
    newPassword: newPassword,
    confirmNewPassword: confirmNewPassword
  };
  const validated = validate(validatedData, constrains);
  const onClickBtn = () => {
    if (validated) {
      ObjToArr(validated).map(item => {
        message.error(item);
        return 0;
      });
    } else {
      const changePassword = async body => {
        const res = await Auth.resetPassword(body);
        if (res) {
          return alert('berhasil');
        }
        return alert('gagal');
      };
      const userData = JSON.parse(getCookie('userData')).data;
      const body = {
        email: userData.user.email,
        token: userData.user.id,
        password: newPassword
      };
      changePassword(body);
    }
  };
  return (
    <LoginFormWrapper>
      <TopHeader title={'Rubah Password'} />
      <CenterLayout width={'400px'} className={'container_register_form'}>
        <Card bordered={false} className={'container_form_register'}>
          <Form>
            <InputPassword
              placeholder={'Masukkan Password Lama'}
              value={oldPassword}
              onChange={e => setOldPassword(e.target.value)}
            />
            <InputPassword
              placeholder={'Masukkan Password Baru'}
              value={newPassword}
              onChange={e => setNewPassword(e.target.value)}
            />
            <InputPassword
              placeholder={'Ulangi Lagi Password Baru'}
              value={confirmNewPassword}
              onChange={e => setConfirmNewPassword(e.target.value)}
            />
            <Button text={'RUBAH SEKARANG'} onClick={onClickBtn} />
          </Form>
        </Card>
      </CenterLayout>
    </LoginFormWrapper>
  );
};

export default Index;
