import React, { useState } from 'react';
import { TopHeader, CenterLayout, UploadButton } from '../../components';
import { WaitingForPaymentWrapper } from './style';
import { File } from '../../services';
import { getCookie } from '../../utils/cookies';
import { message, Skeleton } from 'antd';
// import image from '../../assets/img'

import Detail from './detail';
import Belanjaan from './belanjaan';

function Index() {
  const [item, setItem] = useState({
    name: 'Mangga Manalagi',
    price: 17000,
    perItem: 'Kg',
    count: 3
  });
  const [biayaAntar] = useState(10000);
  const [paymentReceiptImage, setPaymentReceiptImage] = useState();
  const [isFetching, setIsFetching] = useState(false);
  const defaultPaymentReceiptImage =
    'https://via.placeholder.com/500x400?text=Upload-Image';
  const onUpload = e => {
    const imageUrl = URL.createObjectURL(e.file.originFileObj);
    const form = e.file.originFileObj;
    const bucketName = JSON.parse(getCookie('userData')).data.user.bucket_name;
    const objectName = e.file.name;
    setIsFetching(true);
    const uploadFile = () => {
      File.uploadReq(bucketName, objectName)
        .then(res => {
          const uploadFile = () => {
            File.uploadFile(res.data.url, form)
              .then(() => {
                setPaymentReceiptImage(imageUrl);
              })
              .catch(() => {
                message.error('something went wrong');
              })
              .finally(() => {
                setIsFetching(false);
              });
          };
          uploadFile();
          message.success('Image File Uploaded Successfully');
        })
        .catch(() => {
          message.error('something went wrong');
        });
    };
    uploadFile();
  };
  return (
    <WaitingForPaymentWrapper>
      <TopHeader title={'Detail Transaksi'} />
      <CenterLayout width={'570px'}>
        <div className="container_waiting_for_payment_content">
          <Detail />
          <Belanjaan item={item} setItem={setItem} biayaAntar={biayaAntar} />
          <div className="container_nota">
            {isFetching ? (
              <Skeleton active />
            ) : (
              <img
                src={
                  paymentReceiptImage
                    ? paymentReceiptImage
                    : defaultPaymentReceiptImage
                }
                alt=""
              />
            )}
          </div>
        </div>
      </CenterLayout>
      <UploadButton
        type="file"
        text={'UPLOAD BUKTI TRANSFER'}
        onChange={onUpload}
        disabled={isFetching}
      />
    </WaitingForPaymentWrapper>
  );
}

export default Index;
