import React, { useState } from 'react';
import { useHistory } from 'react-router';
import { LeftSideBar } from '../../components';
import { MenuWrapper } from './style';
import { Menu, message } from 'antd';
import image from '../../assets/img';
import { AuthenticationService } from '../../services';

const Index = props => {
  const history = useHistory();
  const [leftMenus] = useState([
    {
      name: 'Home',
      path: '/'
    },
    {
      name: 'Daftar Transaksi',
      path: '/list-transaction'
    },
    {
      name: 'Contact Us',
      path: '/'
    },
    {
      name: 'Term and Agreement',
      path: '/terms'
    },
    {
      name: 'Change Password',
      path: '/change-password'
    }
  ]);
  const leftMenusRendered = leftMenus.map((item, i) => {
    const onChangeMenu = () => {
      history.push(item.path);
    };
    return (
      <Menu.Item key={i} onClick={onChangeMenu}>
        <span>{item.name}</span>
      </Menu.Item>
    );
  });
  const onLogout = () => {
    AuthenticationService.Logout();
    message.success('Logout Success');
    history.push('/login');
  };
  return (
    <LeftSideBar
      imgProfile={image.orange}
      name={'Budi Binomo'}
      phone={'081 234 567'}
      show={props.show}
      onHide={props.onHide}
      onChangeProfile={() => history.push('/change-profile')}
      onLogout={onLogout}
    >
      <MenuWrapper>
        <Menu
          defaultSelectedKeys={props.activeMenu}
          mode={'vertical'}
          className="container_menu"
          onClick={props.onChangeActiveMenu}
          selectedKeys={props.activeMenu}
        >
          {leftMenusRendered}
        </Menu>
      </MenuWrapper>
    </LeftSideBar>
  );
};

export default Index;
