import styled from 'styled-components';

export const ButtonWrapper = styled.div`
  display: inline-block;
  // border: 1px solid green;
  border-radius: 4px;
  font-size: 13px;
  font-weight: 600;
  text-align: center;
  .button__text {
    color: var(--white-two);
    font-weight: bold;
    cursor: pointer;
    padding: 5px 30px;
    background-image: linear-gradient(to top, #50eb2c, #18ab26);
    border: none;
    width: 100%;
    height: 100%;
    border-radius: 4px;
  }
  .button__loading {
    height: 20px;
    vertical-align: middle;
  }
  .button__text.disabled {
    opacity: 0.7;
    cursor: auto;
  }
`;
