import Main from '../pages/mainPages';
import Login from '../pages/login';
import Register from '../pages/register';
import ListTransaction from '../pages/listTransaction';
import DetailProduct from '../pages/detailProduct';
import Checkout from '../pages/checkout';
import WaitingForPayment from '../pages/waitingForPayment';
import TransactionDetail from '../pages/transactionDetail';
import SearchResult from '../pages/searchResult';
import ChangePassword from '../pages/changePassword';
import ForgotPassword from '../pages/forgotPassword';
import Terms from '../pages/terms';
import ChangeProfile from '../pages/changeProfile';
import AddAddress from '../pages/addAddress';
import EditAddress from '../pages/editAddress';
import ListAddress from '../pages/listAddress';
import Category from '../pages/category';

const routes = [
  // public Routes
  { path: '/login', component: Login, isPublic: true },
  { path: '/register', component: Register, isPublic: true },
  { path: '/terms', component: Terms, isPublic: true },

  // private Routes
  { path: '/change-profile', component: ChangeProfile, isPublic: false },
  { path: '/change-password', component: ChangePassword, isPublic: false },
  { path: '/forgot-password', component: ForgotPassword, isPublic: false },
  { path: '/add-address', component: AddAddress, isPublic: false },
  { path: '/edit-address/:id', component: EditAddress, isPublic: false },
  { path: '/list-address', component: ListAddress, isPublic: false },
  { path: '/list-transaction', component: ListTransaction, isPublic: false },
  { path: '/product-detail/:id', component: DetailProduct, isPublic: false },
  {
    path: '/waiting-for-payment',
    component: WaitingForPayment,
    isPublic: false
  },
  { path: '/checkout', component: Checkout, isPublic: false },
  { path: '/search', component: SearchResult, isPublic: false },
  {
    path: '/transaction-detail',
    component: TransactionDetail,
    isPublic: false
  },
  { path: '/category/:id', component: Category, isPublic: false },
  { path: '/', component: Main, isPublic: false }
];

export default routes;
