import baseService from '../baseServices';
import baseServiceNoAuth from '../baseServiceNoAuth';
import { URL } from '../../config/rest';

const downloadReq = body => {
  return baseService.post(URL.DOWNLOAD_FILE, body);
};

const uploadReq = (bucketName, objectName) => {
  const body = {
    bucketName: bucketName,
    objectName: objectName
  };
  return baseService.post(URL.UPLOAD_FILE, body);
};

const uploadFile = (url, body) => {
  return baseServiceNoAuth.put(url, body);
};

export default {
  downloadReq,
  uploadFile,
  uploadReq
};
