import React from 'react';
import { Icon } from 'antd';
import { PickAddressPopUpWrapper } from './pickAdress.style';
import { PickAddressItemWrapper } from './pickAdressItem.style';
import { CenterLayout } from '../';

const PickAddressPopUp = props => {
  return (
    <PickAddressPopUpWrapper show={props.show}>
      <CenterLayout width={'360px'}>
        <div className="component_pick_address_pop_up">
          <div className="component_pick_address_header">
            <span className="component_pick_address_title">
              Silahkan pilih alamat pengirima yang sudah kamu gunakan sebelumnya
            </span>
            <span
              className="component_pick_address_close"
              onClick={
                props.onHide === undefined ? '' : () => props.onHide(false)
              }
            >
              <Icon type="close" />
            </span>
          </div>
          <div className="component_pick_address_children">
            {props.children}
            <div>
              <span className="component_pick_address_add">
                + Tambahkan Alamat Baru
              </span>
            </div>
          </div>
        </div>
      </CenterLayout>
      <div
        className="ant-modal-mask"
        onClick={props.onHide === undefined ? '' : () => props.onHide(false)}
      ></div>
    </PickAddressPopUpWrapper>
  );
};

const PickAddressItem = props => {
  return (
    <PickAddressItemWrapper onClick={props.onClick}>
      {props.text}
      {props.isModify && (
        <div
          className="component_pick_address_item_modify"
          onClick={e => e.stopPropagation()}
        >
          <span onClick={props.onClickEdit}>
            <Icon type="edit" />
          </span>
          <span onClick={props.onClickDelete}>
            <Icon type="delete" />
          </span>
        </div>
      )}
    </PickAddressItemWrapper>
  );
};

export { PickAddressPopUp, PickAddressItem };
