import React from 'react';
import { CheckoutItemWrapper } from './style';
import { CheckoutDescWrapper } from './checkoutDesc.style';
import { CheckoutDescBoldWrapper } from './checkoutDescBold.style';
import { CheckoutNavButtonWrapper } from './checkoutNavButton.style';
import { Icon } from 'antd';
import { ToPrice } from '../../utils/converter';

import { CenterLayout, Button } from '../';

function CheckoutItem(props) {
  return (
    <CheckoutItemWrapper className={props.className}>
      <div className="component_checkout_item_left">
        <div
          className="component_checkout_item_image"
          style={{ backgroundImage: `url('${props.imgItem}')` }}
        ></div>
        <div className="component_checkout_item_text">
          <div className="component_checkout_item_title">{props.title}</div>
          <div className="component_checkout_item_price_per">
            {ToPrice(props.price)} / {props.perItem}
          </div>
          <div className="component_checkout_item_total">
            {ToPrice(props.price * props.count)}
          </div>
        </div>
      </div>
      {!props.hideModify && (
        <div className="component_checkout_item_right">
          {props.disabled ? (
            <span className="component_checkout_item_icon disabled">
              <Icon type="minus" />
            </span>
          ) : (
            <span
              className="component_checkout_item_icon"
              onClick={props.onMin}
            >
              <Icon type="minus" />
            </span>
          )}
          <span className="component_checkout_item_count">{props.count}</span>
          {props.disabled ? (
            <span className="component_checkout_item_icon disabled">
              <Icon type="plus" />
            </span>
          ) : (
            <span
              className="component_checkout_item_icon"
              onClick={props.onAdd}
            >
              <Icon type="plus" />
            </span>
          )}
          {props.disabled ? (
            <span className="component_checkout_item_icon_delete disabled">
              <Icon type="delete" />
            </span>
          ) : (
            <span
              className="component_checkout_item_icon_delete"
              onClick={props.onDelete}
            >
              <Icon type="delete" />
            </span>
          )}
        </div>
      )}
    </CheckoutItemWrapper>
  );
}

const CheckoutDesc = props => {
  return (
    <CheckoutDescWrapper>
      <div className="component_checkout_desc_text">{props.text}</div>
      <div className="component_checkout_desc_total">
        {ToPrice(props.total)}, -
      </div>
    </CheckoutDescWrapper>
  );
};

const CheckoutDescBold = props => {
  return (
    <CheckoutDescBoldWrapper>
      <div className="component_checkout_desc_text">{props.text}</div>
      <div className="component_checkout_desc_total">
        {ToPrice(props.total)}, -
      </div>
    </CheckoutDescBoldWrapper>
  );
};

const CheckoutNavButton = props => {
  return (
    <CheckoutNavButtonWrapper>
      <CenterLayout width={'80%'}>
        <div className="component_checkout_nav_button_text">
          <span>Total Pembayaran</span>
          <span>{ToPrice(props.total)}</span>
        </div>
        {props.disabled ? (
          <Button
            text={'PESAN SEKARANG'}
            className={'component_checkout_nav_buy disabled'}
            btnClassName={'component_checkout_item_nav_buy_button'}
          />
        ) : (
          <Button
            onClick={props.onClick}
            text={'PESAN SEKARANG'}
            className={'component_checkout_nav_buy'}
            btnClassName={'component_checkout_item_nav_buy_button'}
          />
        )}
      </CenterLayout>
    </CheckoutNavButtonWrapper>
  );
};

export { CheckoutItem, CheckoutDesc, CheckoutDescBold, CheckoutNavButton };
