import React, { useState } from 'react';
import { FormWrapper } from './style';
import { CenterLayout, InputForm, Button, TopHeader } from '../../components';
import { Form, Card, message } from 'antd';
import { Auth } from '../../services';
import validate from 'validate.js';

const Index = props => {
  const [email, setEmail] = useState('');
  const constraints = {
    email: {
      presence: true,
      email: {
        message: 'is not valid'
      }
    }
  };
  const onNext = () => {
    const isNotValid = validate({ email: email }, constraints);
    if (isNotValid) {
      message.error(isNotValid.email[0]);
    } else {
      const forgotPassword = async body => {
        const res = await Auth.forgotPassword(body);
        console.log(res);
      };
      const body = {
        email: email
      };
      forgotPassword(body);
    }
  };
  return (
    <FormWrapper>
      <TopHeader title={'Lupa Password'} />
      <CenterLayout width={'400px'} className={'container_register_form'}>
        <Card bordered={false} className={'container_form_register'}>
          <Form>
            <InputForm
              placeholder={'Masukkan Alamat Email'}
              value={email}
              onChange={e => setEmail(e.target.value)}
            />
            <Button text={'SELANJUTNYA'} onClick={onNext} />
          </Form>
        </Card>
      </CenterLayout>
    </FormWrapper>
  );
};

export default Index;
