import React from 'react'
import { TextWrapper } from './style'

const Text = (props) => {
	return(
		<TextWrapper className={ props.className }>
			{ props.children }
		</TextWrapper>
	)
}

const TextCenter = (props) => {
	return(
		<Text className={ props.className }>
			<div className="component_text_center">
				{ props.children }
			</div>
		</Text>
	)
}

export {
	TextCenter,
	Text
}