import React from 'react';
import { NotificationWrapper } from './style';
import { NotificationItemWrapper } from './notificationItemWrapper.style';

import { Badge, Icon } from 'antd';
import { ClickOut } from '../';

const NotificationItem = props => {
  return <NotificationItemWrapper>{props.text}</NotificationItemWrapper>;
};

const Notification = props => {
  return (
    <NotificationWrapper>
      <Badge count={props.count}>
        <div
          className="component_notification_box"
          onClick={() => props.onShow(!props.showItems)}
        >
          <Icon type="bell" style={{ fontSize: '20px' }} />
        </div>
      </Badge>
      <ClickOut onClickOutside={() => props.onShow(false)}>
        <div
          className="component_notification_dropdown_wrapper"
          style={{ display: props.showItems ? 'block' : 'none' }}
        >
          <div className="component_notification_dropdown_title">
            Notification&nbsp;
            {props.count > 0 && `(${props.count})`}
          </div>
          <div className="component_notification_dropdown_items">
            {props.children}
          </div>
        </div>
      </ClickOut>
    </NotificationWrapper>
  );
};

export { Notification, NotificationItem };
