import React, { useState } from 'react';
import { HeaderWrapper } from './header.style';
import { TopHeader } from '../../components';
import { Icon } from 'antd';
import { LeftSideBar } from '../';

function Index() {
  const [showLeftSideBar, setShowLeftSideBar] = useState(false);
  const [leftMenusActive, setLeftMenusActive] = useState('1');
  function changeLeftMenusActive(x) {
    setLeftMenusActive(x.key);
  }
  return (
    <HeaderWrapper>
      <LeftSideBar
        show={showLeftSideBar}
        activeMenu={[leftMenusActive]}
        onHide={setShowLeftSideBar}
        onChangeActiveMenu={changeLeftMenusActive}
      />
      <TopHeader
        title={'Daftar Transaksi'}
        icon={<Icon type="menu" />}
        onBack={() => setShowLeftSideBar(true)}
      />
    </HeaderWrapper>
  );
}

export default Index;
