import baseService from '../baseServices';
import { URL } from '../../config/rest';

const getTransaction = () => {
  let parameters = {
    params: {
      page: 1,
      maxResult: 100
    }
  };
  return baseService.get(URL.TRANSACTION, parameters);
};

const createTransaction = body => {
  return baseService.post(URL.TRANSACTION, body);
};

export default {
  getTransaction,
  createTransaction
};
