import React from 'react';
import { Card, Skeleton, Empty } from 'antd';
import { PaymentMethodWrapper } from './paymentMethod.style';
import {
  CardHeader,
  PaymentMethodOption,
  PaymentMethodBox
} from '../../components';
import image from '../../assets/img';

const Index = props => {
  const banksRendered = props.banks.map((item, i) => {
    const onChange = () => {
      props.setActiveBank(i);
      props.setPaymentFlag(item.id);
    };
    return (
      <PaymentMethodOption
        img={props.bankImages[i]}
        name={item.name}
        isSelected={props.activeBank === i}
        onClick={onChange}
        key={i}
      />
    );
  });
  return (
    <PaymentMethodWrapper>
      <Card bordered={false}>
        <CardHeader text={'Metode Pembayaran'} />
        <div className="container_transfer_option">
          <PaymentMethodBox
            text={'Transfer Manual'}
            isSelected={props.activePaymentMethod === 0}
            onClick={() => props.setActivePaymentMethod(0)}
          />
          <PaymentMethodBox
            img={image.duitku}
            isSelected={props.activePaymentMethod === 1}
            onClick={() => props.setActivePaymentMethod(1)}
          />
        </div>
        {props.activePaymentMethod === 0 && props.isBankFethcing ? (
          <Skeleton active />
        ) : props.banks === 0 ? (
          <Empty />
        ) : (
          banksRendered
        )}
      </Card>
    </PaymentMethodWrapper>
  );
};

export default Index;
