import React, { useState } from 'react';
import useAsyncEffect from 'use-async-effect';
import { CenterLayout, Product } from '../../components';
import { ProductsWrapper } from './products.style';
import { ProductsContainerWrapper } from './productsContainer.style';
import { ToPrice } from '../../utils/converter';
import { Product as ProductService, File, Cart } from '../../services';
import { message, Skeleton, Empty } from 'antd';
import { getCookie } from '../../utils/cookies';

function Index(props) {
  const { match, params } = props;
  const [products, setProducts] = useState([]);
  const [productImages, setProductImages] = useState([]);
  const [count, setCount] = useState(0);
  const [modifyItem, setModifyItem] = useState(-1);
  const [isFetching, setIsFetching] = useState(false);
  useAsyncEffect(
    async isMounted => {
      const getProducts = async districtCode => {
        setIsFetching(true);
        await ProductService.getProductByDistrict(districtCode)
          .then(res => {
            setProducts(res.data);
            res.data.map(item => {
              const bucketName = item.bucket_name;
              const objectName = item.object_name;
              const body = {
                bucketName: bucketName,
                objectName: objectName
              };
              const downloadUrl = async () => {
                try {
                  const res = await File.downloadReq(body);
                  setProductImages(prevState => prevState.concat(res.data.url));
                } catch (error) {
                  item.image = '';
                }
              };
              downloadUrl(body);
              return 0;
            });
          })
          .catch(() => {
            message.error('Something Went Wrong');
          })
          .finally(() => {
            setIsFetching(false);
          });
      };
      const getProductsByCategory = async () => {
        setIsFetching(true);
        await ProductService.getProductByCategory(match.params.id)
          .then(res => {
            setProducts(res.data);
            res.data.map(item => {
              const bucketName = item.bucket_name;
              const objectName = item.object_name;
              const body = {
                bucketName: bucketName,
                objectName: objectName
              };
              const downloadUrl = async () => {
                try {
                  const res = await File.downloadReq(body);
                  setProductImages(prevState => prevState.concat(res.data.url));
                } catch (error) {
                  item.image = '';
                }
              };
              downloadUrl(body);
              return 0;
            });
          })
          .catch(() => {
            message.error('Something Went Wrong');
          })
          .finally(() => {
            setIsFetching(false);
          });
      };
      const getProductByKeyword = async keyword => {
        setIsFetching(true);
        await ProductService.getProductByKeyword(keyword, 100)
          .then(res => {
            setProducts(res.data);
            res.data.map(item => {
              const bucketName = item.bucket_name;
              const objectName = item.object_name;
              const body = {
                bucketName: bucketName,
                objectName: objectName
              };
              const downloadUrl = async () => {
                try {
                  const res = await File.downloadReq(body);
                  setProductImages(prevState => prevState.concat(res.data.url));
                } catch (error) {
                  item.image = '';
                }
              };
              downloadUrl(body);
              return 0;
            });
          })
          .catch(() => {
            message.error('Something Went Wrong');
          })
          .finally(() => {
            setIsFetching(false);
          });
      };
      if (!isMounted()) return;
      if (match) {
        getProductsByCategory();
      } else {
        if (params.district_code !== undefined) {
          getProducts(params.district_code);
        } else if (params.query !== undefined) {
          getProductByKeyword(params.query);
        } else if (getCookie('location') !== '') {
          getProducts(getCookie('location'));
        }
      }
    },
    [match ? match.params.id : '', params]
  );
  const productsRendered = products.map((item, i) => {
    const onBuy = () => {
      const addToCart = async id => {
        await Cart.postCart(id)
          .then(res => {
            message.success('Product Added');
            // console.log(res.data);
          })
          .catch(() => {
            message.error('Something went wrong');
          });
      };
      addToCart(item.id);
      setCount(item.price.minQty);
      setModifyItem(i);
    };
    const onAdd = () => {
      if (count < item.price.stock) {
        setCount(count + 1);
      } else {
        message.error('Out of stock');
      }
    };
    const onMin = () => {
      if (count === 0 || count === item.price.minQty) {
        setModifyItem(-1);
      }
      setCount(count - 1);
    };
    return (
      <Product
        title={item.name}
        src={productImages[i]}
        label={'Tanikan Fresh'}
        desc={item.shortDescription}
        discountPrice={ToPrice(item.price.amountWithDiscount)}
        mainPrice={ToPrice(item.price.amount)}
        perItem={item.price.minQty}
        minPurchase={1}
        unit={item.unit}
        key={i}
        showModifyItem={modifyItem === i}
        toProductDetail={`/product-detail/${item.id}`}
        count={count}
        onBuy={onBuy}
        onAdd={onAdd}
        onMin={onMin}
      />
    );
  });
  return (
    <ProductsContainerWrapper>
      <CenterLayout>
        {isFetching ? (
          <Skeleton active />
        ) : products.length === 0 ? (
          <Empty />
        ) : (
          <ProductsWrapper>{productsRendered}</ProductsWrapper>
        )}
      </CenterLayout>
    </ProductsContainerWrapper>
  );
}

export default Index;
