import React, { useState, useEffect } from 'react';
import { message, Skeleton } from 'antd';
import { CarouselWrapper } from './style';
import { CenterLayout } from '../../components';
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import { Banner, File } from '../../services';
import { default as imageAsset } from '../../assets/img';

function CarouselItem({ banner, image = imageAsset.banner }) {
  return (
    <div>
      <img alt={banner.name} src={image} style={{ backgroundColor: '#fff' }} />
    </div>
  );
}

function CarouselContainer() {
  const [images, setImages] = useState([]);
  const [banners, setBanners] = useState([]);
  const [isFetching, setIsFetching] = useState(false);
  useEffect(() => {
    const GetBanner = async () => {
      setIsFetching(true);
      await Banner.get()
        .then(res => {
          setBanners(res.data);
          res.data.map(item => {
            const bucketName = item.bucket_name;
            const objectName = item.object_name;
            const body = {
              bucketName: bucketName,
              objectName: objectName
            };
            const downloadUrl = async () => {
              await File.downloadReq(body).then(res => {
                setImages(images => [...images, res.data.url]);
              });
            };
            downloadUrl();
            return 0;
          });
        })
        .catch(() => {
          message.error('Something went wrong');
        })
        .finally(() => {
          setIsFetching(false);
        });
    };
    GetBanner();
  }, []);

  const carouselRendered = banners.map((banner, i) => {
    return <CarouselItem key={i} banner={banner} image={images[i]} index={i} />;
  });

  return (
    <CarouselWrapper>
      <CenterLayout>
        {isFetching ? (
          <Skeleton active />
        ) : (
          <Carousel
            autoPlay
            infiniteLoop={true}
            showThumbs={false}
            showStatus={false}
          >
            {carouselRendered}
          </Carousel>
        )}
      </CenterLayout>
    </CarouselWrapper>
  );
}

export default CarouselContainer;
