import React, { useState } from 'react';
import { CategoriesWrapper } from './style';
import { CenterLayout, CategoryCard } from '../../components';
import { Affix, message, Skeleton } from 'antd';
import { Category, File } from '../../services';
import { useHistory } from 'react-router-dom';
import useAsyncEffect from 'use-async-effect';

function Categories(props) {
  const history = useHistory();

  const [categories, setCategories] = useState([]);
  const [imageCategories, setImageCategories] = useState([]);
  const [isSpecial] = useState(0);
  const [isScrolled, setScrolled] = useState(false);
  const [isFetching, setIsFetching] = useState(false);
  const [reFetch] = useState(false);
  useAsyncEffect(
    async isMounted => {
      const getCategories = async () => {
        setIsFetching(true);
        await Category.get()
          .then(res => {
            setCategories(res.data);
            res.data.map(item => {
              const bucketName = item.bucket_name;
              const objectName = item.object_name;
              const body = {
                bucketName: bucketName,
                objectName: objectName
              };
              const downloadUrl = async () => {
                try {
                  const res = await File.downloadReq(body);
                  setImageCategories(prevState =>
                    prevState.concat(res.data.url)
                  );
                } catch (error) {
                  message.error('someting went wrong');
                }
              };
              downloadUrl(body);
              return 0;
            });
          })
          .catch(() => {
            message.error('Something went wrong');
          })
          .finally(() => {
            setIsFetching(false);
          });
      };
      if (!isMounted()) return;
      getCategories();
    },
    [reFetch]
  );
  const categoriesRendered = categories.map((item, i) => {
    return (
      <CategoryCard
        src={imageCategories[i]}
        text={item.name}
        special={isSpecial === i}
        onClick={() => history.push(`/category/${item.id}`)}
        key={i}
      />
    );
  });
  return (
    <CategoriesWrapper isScrolled={isScrolled}>
      {!props.isNotRoot && (
        <Affix onChange={affixed => setScrolled(affixed)} offsetTop={48}>
          <div className={'component_categories_wrapper_affix'}>
            <CenterLayout>{categoriesRendered}</CenterLayout>
          </div>
        </Affix>
      )}
      {!props.isNotRoot && (
        <CenterLayout>
          {isFetching ? (
            <Skeleton active />
          ) : (
            <div className={'component_categories_wrapper'}>
              {categoriesRendered}
            </div>
          )}
        </CenterLayout>
      )}
    </CategoriesWrapper>
  );
}

export default Categories;
