import React from 'react';
import { HeaderWrapper } from './style'

const ContentHeader = (props) => {
	return(
		<HeaderWrapper>
			<h2>{ props.text }</h2>
		</HeaderWrapper>
	)
}

const ComponentHeader = (props) => {
	return(
		<HeaderWrapper>
			<h3>{ props.text }</h3>
		</HeaderWrapper>
	)
}

const CardHeader = (props) => {
	return(
		<HeaderWrapper>
			<h3 className="component_card_header">{ props.text }</h3>
		</HeaderWrapper>
	)
}

export {
	ContentHeader,
	ComponentHeader,
	CardHeader
}