import React from 'react';
import { Transaction } from '../../components';
import { Empty, Skeleton } from 'antd';
import { PrintDate } from '../../utils/timeParser';

function Index(props) {
  const transactionRendered = props.itemList.map((item, i) => {
    return (
      <Transaction
        code={item.id}
        transactionDate={PrintDate(item.created_at)}
        total={item.total_amount}
        key={i}
      />
    );
  });
  return (
    <div>
      {props.isFetching ? (
        <Skeleton active />
      ) : props.itemList.length === 0 ? (
        <Empty />
      ) : (
        transactionRendered
      )}
    </div>
  );
}

export default Index;
