import React from 'react';
import { RecommendationProductWrapper } from './recomendationProduct.style';
import {
  CenterLayout,
  ComponentHeader,
  Product
} from '../../components';
import { Empty, Skeleton } from 'antd';
import { ToPrice } from '../../utils/converter';
// import { getCookie } from '../../utils/cookies';
// import { SumObjOfArr } from '../../utils/converter';

const Index = props => {
  // const [items] = useState(JSON.parse(getCookie('items')));
  const { products, isFetching, productImages } = props;
  const productsRendered = products.map((item, i) => {
    return (
      <Product
        title={item.name}
        src={productImages[i]}
        label={'Tanikan Fresh'}
        desc={item.shortDescription}
        discountPrice={ToPrice(item.discountPrice)}
        mainPrice={ToPrice(item.mainPrice)}
        perItem={1}
        minPurchase={1}
        key={i}
        unit={item.unit}
        toProductDetail={`/product-detail/${item.id}`}
      />
    );
  });
  return (
    <RecommendationProductWrapper>
      <CenterLayout>
        <ComponentHeader text={'Produk Serupa'} />
        {isFetching ? (
          <Skeleton active />
        ) : products.length === 0 ? (
          <Empty />
        ) : (
          <div className="container_products_wrapper">{productsRendered}</div>
        )}
      </CenterLayout>
    </RecommendationProductWrapper>
  );
};

export default Index;
