import React from 'react';
import { Card, DatePicker, Input, Skeleton } from 'antd';
import {
  CardHeader,
  FormGroup,
  DeliveryTimeBox,
  FormGroupInline
} from '../../components';
import { ShipmentWrapper } from './shipment.style';
const { TextArea } = Input;

const Index = props => {
  function handleDatePickerChange(date, dateString, i) {
    props.setSendDate(date);
  }
  const deliveryTimeBoxRendered = props.deliveryTime.map((item, i) => {
    const onChange = () => {
      props.setActiveDeliveryTime(i);
      props.setDeliveryTimeId(item.id);
    };
    return (
      <DeliveryTimeBox
        text={item.description}
        isActive={props.activeDeliveryTime === i}
        onClick={onChange}
        key={i}
      />
    );
  });
  return (
    <ShipmentWrapper>
      <Card bordered={false}>
        <CardHeader text={'Pengiriman'} />
        <FormGroup title={'Tanggal Kirim'}>
          <DatePicker
            defaultValue={props.sendDate}
            defaultPickerValue={props.sendDate}
            onChange={handleDatePickerChange}
          />
        </FormGroup>
        <FormGroupInline title={'Pilih Jam Antar'}>
          {props.isDeliveryTimeFetching ? (
            <Skeleton active />
          ) : (
            deliveryTimeBoxRendered
          )}
        </FormGroupInline>
        <FormGroup title={'Kota'} isInline={true}>
          Jakarta Barat
        </FormGroup>
        <FormGroup
          title={'Alamat'}
          topRightBtn={'Pilih Alamat'}
          onTopRightBtn={() => props.setShowPickAddress(true)}
        >
          <TextArea
            rows={4}
            placeholder={'Pilih Alamat Anda'}
            autoSize={{ minRows: 6, maxRows: 6 }}
            disabled={true}
            value={props.activeAddress}
          ></TextArea>
        </FormGroup>
      </Card>
    </ShipmentWrapper>
  );
};

export default Index;
