import React, { useState } from 'react';
import { Card, Empty, Skeleton, message, Modal } from 'antd';
import { GroceriesWrapper } from './groceries.style';
import {
  CardHeader,
  CheckoutItem,
  CheckoutDesc,
  CheckoutDescBold
} from '../../components';
import { Cart } from '../../services';
const { confirm } = Modal;

// import image from '../../assets/img';
// import { SumObj } from '../../utils/converter'

const Index = props => {
  const [isDisabledModifyItem, setIsDisabledModifyItem] = useState(false);
  const checkoutItemRendered = props.items.map((item, i) => {
    const updateCartItem = async count => {
      setIsDisabledModifyItem(true);
      await Cart.updateCartItemByProductId(item.id, count)
        .then(() => 1)
        .catch(() => message.error('Something went wrong'))
        .finally(() => {
          setIsDisabledModifyItem(false);
        });
    };
    const showDeleteConfirm = () => {
      confirm({
        title: 'Apakah anda ingin menghapus barang ini ?',
        okText: 'Ya',
        okType: 'danger',
        cancelText: 'Tidak',
        onOk() {
          const deleteById = async id => {
            await Cart.deleteCart(id)
              .then(() => {
                message.success('Produk Berhasil Dihapus');
              })
              .catch(() => message.error('something went wrong'))
              .finally(() => {
                props.setIsReFetch(!props.isReFetch);
              });
          };
          deleteById(item.id);
        }
      });
    };
    return (
      <CheckoutItem
        key={i}
        imgItem={props.images[i]}
        title={item.product.name}
        price={item.product === undefined ? item.product.price : 0}
        perItem={1}
        count={item.qty}
        onMin={() => updateCartItem(item.qty === 0 ? 0 : item.qty - 1)}
        onAdd={() => updateCartItem(item.qty + 1)}
        onDelete={showDeleteConfirm}
        disabled={isDisabledModifyItem}
      />
    );
  });
  return (
    <GroceriesWrapper>
      <Card bordered={false}>
        <CardHeader text={'Belanjaan'} />
        <div className="container_checkout_item">
          {props.isFetching ? (
            <Skeleton active />
          ) : props.items === 0 ? (
            <Empty />
          ) : (
            checkoutItemRendered
          )}
        </div>
        <div className="container_checkout_detail">
          <CheckoutDesc text={'Biaya'} total={10000} />
          <CheckoutDesc text={'Biaya Antar'} total={props.deliveryFee} />
        </div>
        <CheckoutDescBold
          text={'Total Pembayaran'}
          total={10000 + props.deliveryFee}
        />
      </Card>
    </GroceriesWrapper>
  );
};

export default Index;
