import BaseService from '../baseServices';
import { URL } from '../../config/rest';

const getVillage = () => {
  let parameters = {
    params: {
      page: 1,
      maxResult: 100
    }
  };
  return BaseService.get(URL.SUBDISTRICT, parameters);
};

const getVillageBySubDistrict = id => {
  let parameters = {
    params: {
      page: 1,
      maxResult: 100,
      subdistrict_code: id
    }
  };
  return BaseService.get(URL.VILLAGE, parameters);
};

export default {
  getVillage,
  getVillageBySubDistrict
};
