import React from 'react';
import { Dropdown, Menu, Icon, Empty } from 'antd';

import { Notification, NotificationItem } from '../../components';
import { DropDownMenuWrapper } from './dropDownMenu.style';
import { setCookie } from '../../utils/cookies';
import { useHistory } from 'react-router';

const Menus = ({ cities, activeCity, setState }) => {
  const history = useHistory();

  const citiesRendered = cities.map((item, i) => {
    return (
      <Menu.Item key={i} value={item.code}>
        {item.name}
      </Menu.Item>
    );
  });
  const onChange = item => {
    setCookie('location', cities[item.key].code);
    setState(cities[item.key].code);
    let location = {
      search: `?district_code=${cities[item.key].code}`
    };
    history.push(location);
  };
  return (
    <Menu onClick={onChange} defaultSelectedKeys={[0]}>
      {citiesRendered}
    </Menu>
  );
};

const Index = ({
  cities,
  activeCity,
  setActiveCity,
  notifications,
  showNotifications,
  setShowNotifications,
  params,
  selectedDistrict,
  setSelectedDistrict
}) => {
  const status = {
    1: 'telah di terima admin',
    2: 'sedang diproses',
    3: 'sukses dikirim',
    4: 'ditolak'
  };
  const notificationsRendered = notifications.map((item, i) => {
    const text = `Pesanan dengan No. Order ${item.id} ${
      status[item.data.transaction_status]
    }`;
    return <NotificationItem text={text} key={i} />;
  });
  return (
    <DropDownMenuWrapper>
      <Dropdown
        overlay={
          <Menus
            cities={cities}
            activeCity={activeCity}
            setState={setActiveCity}
          />
        }
      >
        <div className="ant-dropdown-link" href="#">
          {selectedDistrict}
          <Icon type="down" />
        </div>
      </Dropdown>
      <Notification
        count={notifications.length}
        showItems={showNotifications}
        onShow={setShowNotifications}
      >
        {notifications.length === 0 ? <Empty /> : notificationsRendered}
      </Notification>
    </DropDownMenuWrapper>
  );
};

export default Index;
