import React, { useState, useEffect } from 'react';
import { LoginFormWrapper } from './style';
import {
  CenterLayout,
  InputForm,
  Button,
  TopHeader,
  FormGroup
} from '../../components';
import { Form, Card, Input, message, Skeleton } from 'antd';
import validate from 'validate.js';
import { ObjToArr } from '../../utils/converter';
import { User } from '../../services';
import { getCookie } from '../../utils/cookies';

const { TextArea } = Input;

const LoginForm = () => {
  const [id, setId] = useState('');
  const [name, setName] = useState('');
  const [noHp, setNoHp] = useState('');
  const [email, setEmail] = useState('');
  const [city, setCity] = useState('');
  const [province, setProvince] = useState('');
  const [zipCode, setZipCode] = useState('');
  const [address, setAddress] = useState('');
  const [isFetching, setIsFetching] = useState(false);
  const [reFetch, setReFetch] = useState(false);

  useEffect(() => {
    const userData = JSON.parse(getCookie('userData')).data.user;
    setId(userData.id);
    const getUserById = async () => {
      setIsFetching(true);
      await User.getUserById(userData.id)
        .then(res => {
          setName(res.data.name);
          setEmail(res.data.email);
        })
        .catch(() => {
          message.error('Something went wrong');
        })
        .finally(() => {
          setIsFetching(false);
        });
    };
    getUserById();
  }, [reFetch]);
  const constrains = {
    nama: {
      presence: true,
      length: {
        minimum: 1
      }
    },
    nomorHp: {
      presence: true,
      length: {
        minimum: 11,
        maximum: 13
      },
      numericality: true
    },
    email: {
      email: true
    },
    kodePos: {
      format: /\d{5}(-\d{4})?/
    }
  };
  const validatedData = {
    nama: name,
    nomorHp: noHp,
    email: email,
    kodePos: zipCode
  };
  const onSave = () => {
    const isNotValid = validate(validatedData, constrains);
    if (isNotValid) {
      ObjToArr(isNotValid).map(item => {
        message.error(item);
        return 0;
      });
    } else {
      const updateUserById = async body => {
        await User.updateUserById(id, body)
          .then(res => {
            setReFetch(!reFetch);
            message.success(res.message);
          })
          .catch(() => {
            message.error('Something went wrong');
          });
      };
      const body = {
        name: name,
        email: email,
        object_name: 'Hulk.png'
      };
      updateUserById(body);
    }
  };
  return (
    <LoginFormWrapper>
      <TopHeader title={'Ubah Profile'} />
      <CenterLayout width={'400px'} className={'container_register_form'}>
        <Card bordered={false} className={'container_form_register'}>
          {isFetching ? (
            <Skeleton active />
          ) : (
            <Form>
              <FormGroup title={'Nama'}>
                <InputForm
                  placeholder={'Nama'}
                  value={name}
                  onChange={e => setName(e.target.value)}
                />
              </FormGroup>
              <FormGroup title={'Nomor Hp.'}>
                <InputForm
                  placeholder={'No Hp'}
                  value={noHp}
                  onChange={e => setNoHp(e.target.value)}
                />
              </FormGroup>
              <FormGroup title={'Alamat Email'}>
                <InputForm
                  placeholder={'Email'}
                  value={email}
                  onChange={e => setEmail(e.target.value)}
                />
              </FormGroup>
              <FormGroup title={'Kota/Kecamatan'}>
                <InputForm
                  placeholder={'Cari kota atau kecamatan'}
                  value={city}
                  onChange={e => setCity(e.target.value)}
                />
              </FormGroup>
              <FormGroup title={'Provinsi'}>
                <InputForm
                  placeholder={'Cari Provinsi'}
                  value={province}
                  onChange={e => setProvince(e.target.value)}
                />
              </FormGroup>
              <FormGroup title={'Kode Pos'}>
                <InputForm
                  placeholder={'Masukkan Kode Pos'}
                  value={zipCode}
                  onChange={e => setZipCode(e.target.value)}
                />
              </FormGroup>
              <FormGroup title={'Alamat'}>
                <TextArea
                  placeholder="Alamat"
                  autoSize={{ minRows: 4, maxRows: 8 }}
                  value={address}
                  onChange={e => setAddress(e.target.value)}
                />
              </FormGroup>
              <Button
                text={'SIMPAN PERUBAHAN'}
                className={'container_save_button'}
                onClick={onSave}
              />
            </Form>
          )}
        </Card>
      </CenterLayout>
    </LoginFormWrapper>
  );
};

export default LoginForm;
