import React, { useState } from 'react';
import { LoginFormWrapper } from './style';
import {
  CenterLayout,
  InputForm,
  InputPassword,
  Button,
  TopHeader,
  TextCenter,
  Text
} from '../../components';
import { Form, Card } from 'antd';
import { Login } from './form.handler';
import { Link } from 'react-router-dom';

const LoginForm = props => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const data = {
    email: username.trim(),
    password: password
  };
  return (
    <LoginFormWrapper>
      <TopHeader title={'Login'} />
      <CenterLayout width={'400px'} className={'container_login_form'}>
        <TextCenter className={'container_text_center'}>
          Silahkan login atau daftar terleibh dahulu
          <br />
          untuk melanjutkan.
        </TextCenter>
        <Card bordered={false} className={'container_form_login'}>
          <Form>
            <InputForm
              placeholder={'No Hp'}
              value={username}
              onChange={e => setUsername(e.target.value)}
            />
            <InputPassword
              placeholder={'Password'}
              value={password}
              onChange={e => setPassword(e.target.value)}
            />
            <Button
              text={'Login'}
              onClick={() => Login(data, props.history.push)}
            />
          </Form>
        </Card>
        <Text className={'container_text_forgot_password'}>
          Lupa Password ? <span>Klik Disini</span>
        </Text>
        <Card bordered={false}>
          Belum Punya Akun ?
          <Link to={'/register'}>
            <span className="container_form_login_register">Register</span>
          </Link>
        </Card>
      </CenterLayout>
    </LoginFormWrapper>
  );
};

export default LoginForm;
