import BaseService from '../baseServices';
import { URL } from '../../config/rest';

const getCart = () => {
  let parameters = {
    params: {
      page: 1,
      maxResult: 10
    }
  };
  return BaseService.get(URL.CART, parameters);
};

const updateCartItem = (id, qty = 0) => {
  let body = {
    qty: qty
  };
  return BaseService.put(URL.CART_BY_ID(id), body);
};

const updateCartItemByProductId = (id, qty) => {
  let body = {
    items: [
      {
        qty: qty,
        product_id: id
      }
    ]
  };
  return BaseService.post(URL.CART, body);
};

const postCart = id => {
  return BaseService.post(URL.CART_BY_ID(id));
};

const deleteCart = id => {
  return BaseService.delete(URL.CART_BY_ID(id));
};

export default {
  getCart,
  postCart,
  updateCartItem,
  deleteCart,
  updateCartItemByProductId
};
