import React, { useState, useEffect } from 'react';
import { CheckoutWrapper } from './style';
import {
  TopHeader,
  CenterLayout,
  CheckoutNavButton,
  PickAddressPopUp,
  PickAddressItem
} from '../../components';
import moment from 'moment';
import {
  Bank,
  File,
  Address,
  Cart,
  DeliveryTime,
  Transaction
} from '../../services';

import Shipment from './shipment.container';
import Groceries from './groceries.container';
import PaymentMethod from './paymentMethod.container';
import { message } from 'antd';
import validate from 'validate.js';
import { ObjToArr } from '../../utils/converter';

// import image from '../../assets/img';

const Index = () => {
  const [sendDate, setSendDate] = useState(moment(Date.now()));
  const [deliveryTime, setDeliveryTime] = useState([]);
  const [isDeliveryTimeFetching, setIsisDeliveryTimeFetching] = useState(false);
  const [activeDeliveryTime, setActiveDeliveryTime] = useState(0);
  const [carts, setCarts] = useState([]);
  const [cartImages, setCartImages] = useState([]);
  const [isFetchingCart, setIsFetchingCart] = useState(false);
  const [isRefetchCart, setIsRefetchCart] = useState(false);
  const [deliveryFee] = useState(10000);
  const [banks, setBanks] = useState([]);
  const [isBankFethcing, setIsBankFethcing] = useState(false);
  const [bankImages, setBankImages] = useState([]);
  const [activeBank, setActiveBank] = useState(0);
  const [activePaymentMethod, setActivePaymentMethod] = useState(0);
  const [address, setAddress] = useState([]);
  const [activeAddress, setActiveAddress] = useState('');
  const [showPickAddress, setShowPickAddress] = useState(false);
  const [districtCode, setDistrictCode] = useState('');
  const [paymentFlagId, setpaymentFlagIdId] = useState('');
  const [addressId, setAddressId] = useState('');
  const [deliveryTimeId, setDeliveryTimeId] = useState('');
  useEffect(() => {
    const getBank = async () => {
      await Bank.get()
        .then(res => {
          setpaymentFlagIdId(res.data[activeBank].id);
          setBanks(res.data);
          res.data.map(item => {
            const bucketName = item.bucket_name;
            const objectName = item.object_name;
            const body = {
              bucketName: bucketName,
              objectName: objectName
            };
            const downloadUrl = async () => {
              await File.downloadReq(body).then(res => {
                setBankImages(bankImages => [...bankImages, res.data.url]);
              });
            };
            downloadUrl();
            return 0;
          });
        })
        .catch(() => {
          message.error('something went wrong');
        });
    };
    getBank();
    const getAddress = async () => {
      setIsBankFethcing(true);
      await Address.getAddress()
        .then(res => {
          setAddress(res.data);
        })
        .catch(() => {
          message.error('Something went wrong');
        })
        .finally(() => {
          setIsBankFethcing(false);
        });
    };
    getAddress();
    const getTransaction = async () => {
      setIsFetchingCart(true);
      await Cart.getCart()
        .then(res => {
          setCarts(res.data);
          res.data.map(item => {
            const bucketName = item.product.bucket_name;
            const objectName = item.product.object_name;
            const body = {
              bucketName: bucketName,
              objectName: objectName
            };
            const downloadUrl = async () => {
              await File.downloadReq(body).then(res => {
                setCartImages(prevState => [...prevState, res.data.url]);
              });
            };
            downloadUrl();
            return 0;
          });
        })
        .catch(() => {
          message.error('someting went wrong');
        })
        .finally(() => {
          setIsFetchingCart(false);
        });
    };
    getTransaction();
    const getDeliveryTime = async () => {
      setIsisDeliveryTimeFetching(true);
      await DeliveryTime.get()
        .then(res => {
          setDeliveryTime(res.data);
          setDeliveryTimeId(res.data[activeDeliveryTime].id);
        })
        .catch(() => {
          message.error('Something went wrong');
        })
        .finally(() => {
          setIsisDeliveryTimeFetching(false);
        });
    };
    getDeliveryTime();
  }, [isRefetchCart, activeBank, activeDeliveryTime]);
  const onOrderNow = () => {
    const constrains = {
      district_code: {
        presence: true,
        length: {
          minimum: 1
        }
      },
      payment_flag: {
        presence: true,
        length: {
          minimum: 1
        }
      },
      address_id: {
        presence: true,
        length: {
          minimum: 1
        }
      },
      delivery_time_id: {
        presence: true,
        length: {
          minimum: 1
        }
      }
    };
    const validatedData = {
      district_code: districtCode,
      payment_flag: paymentFlagId,
      address_id: addressId,
      delivery_time_id: deliveryTimeId
    };
    const body = {
      district_code: districtCode,
      payment_flag: paymentFlagId,
      address_id: addressId,
      delivery_time_id: deliveryTimeId,
      transfer_methods: 0
    };
    const isNotValid = validate(validatedData, constrains);
    if (isNotValid) {
      ObjToArr(isNotValid).map(item => {
        message.error(item);
        return 0;
      });
    } else {
      Transaction.createTransaction(body)
        .then(res => {
          message.success('Successfully Created Transaction');
        })
        .catch(() => {
          message.error('something went wrong');
        });
    }
  };
  const addressRendered = address.map((item, i) => {
    const onClickItem = () => {
      setAddressId(item.id);
      setActiveAddress(item.address);
      setShowPickAddress(false);
      setDistrictCode(item.district);
    };
    return (
      <PickAddressItem text={item.address} key={i} onClick={onClickItem} />
    );
  });
  return (
    <CheckoutWrapper>
      <PickAddressPopUp show={showPickAddress} onHide={setShowPickAddress}>
        {addressRendered}
      </PickAddressPopUp>
      <TopHeader title={'Check Out'}></TopHeader>
      <CenterLayout width={'80%'} className={'container_center_layout'}>
        <div className="container_content">
          <Shipment
            sendDate={sendDate}
            setSendDate={setSendDate}
            deliveryTime={deliveryTime}
            activeDeliveryTime={activeDeliveryTime}
            setActiveDeliveryTime={setActiveDeliveryTime}
            activeAddress={activeAddress}
            setShowPickAddress={setShowPickAddress}
            isDeliveryTimeFetching={isDeliveryTimeFetching}
            deliveryTimeId={deliveryTimeId}
            setDeliveryTimeId={setDeliveryTimeId}
            districtCode={districtCode}
            setDistrictCode={setDistrictCode}
            addressId={addressId}
            setAddressId={setAddressId}
          />
          <Groceries
            items={carts}
            setItem={setCarts}
            deliveryFee={deliveryFee}
            isFetching={isFetchingCart}
            images={cartImages}
            isRefetch={isRefetchCart}
            setIsReFetch={setIsRefetchCart}
          />
          <PaymentMethod
            bankImages={bankImages}
            banks={banks}
            activeBank={activeBank}
            setActiveBank={setActiveBank}
            activePaymentMethod={activePaymentMethod}
            setActivePaymentMethod={setActivePaymentMethod}
            isBankFethcing={isBankFethcing}
            paymentFlagId={paymentFlagId}
            setpaymentFlagIdId={setpaymentFlagIdId}
          />
        </div>
      </CenterLayout>
      <CheckoutNavButton total={1 * 2 + deliveryFee} onClick={onOrderNow} />
    </CheckoutWrapper>
  );
};

export default Index;
