import BaseService from '../baseServices';
import { URL } from '../../config/rest';

const getUserById = id => {
  return BaseService.get(URL.USER_BY_ID(id));
};

const updateUserById = (id, body) => {
  return BaseService.put(URL.USER_BY_ID(id), body);
};

export default {
  getUserById,
  updateUserById
};
