import React, { useState, useEffect } from 'react';
import { DetailProductWrapper } from './style';
import { Product, File, Cart } from '../../services';
import { message, Skeleton, Empty } from 'antd';
import { DetailProduct, CenterLayout } from '../../components';
import RecommendationProduct from './remendationProduct.container';

// import image from '../../assets/img';
// import { getCookie } from '../../utils/cookies'

const Index = props => {
  const { match } = props;

  const [buyButton, setBuyButton] = useState(false);
  const [id, setId] = useState('');
  const [label] = useState('Local Farm');
  const [productImage, setProductImage] = useState('');
  const [title, setTitle] = useState('');
  const [subTitle] = useState(
    'Mangga Harus Manis (Baru panen tunggu matang 2-3 hari) (0.9-1.1kb) (2-3pcs)'
  );
  const [discPrice] = useState(15000);
  const [mainPrice] = useState(10000);
  const [unit, setUnit] = useState('');
  const [count, setCount] = useState(0);
  const [stock] = useState(10);
  const [desc, setDesc] = useState('');
  const [isFetching, setIsFetching] = useState(false);
  const [isFetchingSuccess, setIsFetchingSuccess] = useState(true);
  const [productByCategory, setProductByCategory] = useState([]);
  const [productImagesByCategory, setProductImagesByCategory] = useState([]);
  const [
    isFetchingProductByCategory,
    setIsFetchingProductByCategory
  ] = useState(false);
  //   let items = JSON.parse(getCookie('items'))
  //   let item
  //   if (items !== null) {
  //   	item = items.find(({ id }) => id === product.id)
  //   }
  //   const [count, setCount] = useState(item === undefined ? 0 : item.total);
  useEffect(() => {
    const getProductById = async () => {
      setIsFetching(true);
      await Product.getProductById(match.params.id)
        .then(res => {
          setId(res.data.id);
          setTitle(res.data.name);
          setUnit(res.data.unit);
          setDesc(res.data.description);
          //   setDiscPrice(res.data.price.amountWithDiscount);
          //   setMainPrice(res.data.price.amount);
          const getProductByCategory = async () => {
            setIsFetchingProductByCategory(true);
            await Product.getProductByCategory(res.data.category.id)
              .then(res => {
                setProductByCategory(res.data);
                res.data.map(item => {
                  const downloadUrl = async () => {
                    try {
                      const res = await File.downloadReq(body);
                      setProductImagesByCategory(prevState =>
                        prevState.concat(res.data.url)
                      );
                    } catch (error) {
                      item.image = '';
                    }
                  };
                  const bucketName = item.bucket_name;
                  const objectName = item.object_name;
                  const body = {
                    bucketName: bucketName,
                    objectName: objectName
                  };
                  downloadUrl(body);
                  return '';
                });
              })
              .catch(() => {
                message.error('Something went wrong');
              })
              .finally(() => {
                setIsFetchingProductByCategory(false);
              });
          };
          getProductByCategory();
          const bucketName = res.data.bucket_name;
          const objectName = res.data.object_name;
          const body = {
            bucketName: bucketName,
            objectName: objectName
          };
          const downloadUrl = async () => {
            await File.downloadReq(body)
              .then(res => {
                setProductImage(res.data.url);
              })
              .catch(() => {
                message.error('Something went wrong');
              });
          };
          downloadUrl(body);
        })
        .catch(() => {
          message.error('Something went wrong');
          setIsFetchingSuccess(false);
        })
        .finally(() => {
          setIsFetching(false);
        });
    };
    getProductById();
  }, [match.params.id]);
  const onBuy = () => {
    setBuyButton(true);
    const addToCart = async () => {
      await Cart.postCart(id)
        .then(res => {
          // console.log(res.data);
        })
        .catch(() => {
          message.error('Something went wrong');
        });
    };
    addToCart();
  };
  return (
    <div>
      <DetailProductWrapper>
        <CenterLayout>
          {isFetching ? (
            <Skeleton active />
          ) : !isFetchingSuccess ? (
            <Empty />
          ) : (
            <DetailProduct
              id={id}
              imgProduct={productImage}
              label={label}
              title={title}
              subTitle={subTitle}
              discPrice={discPrice}
              mainPrice={mainPrice}
              perItem={unit}
              available={stock}
              desc={desc}
              count={count}
              onCount={setCount}
              showModifyItem={buyButton}
              onShowModifyItem={onBuy}
            />
          )}
        </CenterLayout>
      </DetailProductWrapper>
      <RecommendationProduct
        products={productByCategory}
        productImages={productImagesByCategory}
        isFetching={isFetchingProductByCategory}
      />
    </div>
  );
};

export default Index;
