import BaseService from '../baseServices';
import { URL } from '../../config/rest';

const getSupportedDistrict = () => {
  let parameters = {
    params: {
      page: 1,
      maxResult: 100
    }
  };
  return BaseService.get(URL.SUPPORTED_DISTRICTS, parameters);
};

const getDistrictByProvince = id => {
  let parameters = {
    params: {
      page: 1,
      maxResult: 100,
      province_code: id
    }
  };
  return BaseService.get(URL.DISTRICT, parameters);
};

export default {
  getSupportedDistrict,
  getDistrictByProvince
};
