import React from 'react';
import { DropDownResultWrapper } from './style';
import { Link } from 'react-router-dom';

function DropDownResultSuggestion({ text, linkTo }) {
  return (
    <div className="component_drop_down_result_suggest">
      Semua hasil pencarian &nbsp;
      <Link to={linkTo}>
        <span className="component_dropdown_result_suggest_text">{text}</span>
      </Link>
    </div>
  );
}

export function DropDownResultItem({ text, img, desc, onClick }) {
  return (
    <div className="component_drop_down_result_item">
      <div
        className="component_drop_down_result_item_img"
        style={{ backgroundImage: `url(${img})` }}
        onClick={onClick}
      ></div>
      <div className="component_drop_down_result_item_text">
        <div
          className="component_drop_down_result_item_title"
          onClick={onClick}
        >
          {text}
        </div>
        <div className="component_drop_down_result_item_desc">{desc}</div>
      </div>
    </div>
  );
}

function DropDownResult(props) {
  const { display } = props;
  return (
    <DropDownResultWrapper
      className={'component_dropdown_result_wrapper'}
      style={{
        display: display === undefined || display === false ? 'none' : 'block'
      }}
    >
      <div className="component_dropdown_result_container">
        <DropDownResultSuggestion text={props.text} linkTo={props.linkTo} />
        {props.children}
      </div>
    </DropDownResultWrapper>
  );
}

export default DropDownResult;
