import React from 'react';
import { LeftSideBarWrapper } from './style';
import { Icon } from 'antd';
import { Button } from '../';

const LeftSideBarTitle = ({
  imgProfile,
  name,
  phone,
  onHide,
  onChangeProfile
}) => {
  return (
    <div className="component_leftsidebar_top">
      <span>
        <Icon
          type="close"
          className={'component_leftsidebar_close'}
          onClick={() => onHide(false)}
        />
      </span>
      <div className="component_leftsideBar_profile">
        <div
          className="component_leftsidebar_img"
          style={{ backgroundImage: `url(${imgProfile})` }}
        >
          <span onClick={onChangeProfile}>Ubah</span>
        </div>
        <div className="component_leftsidebar_profile_text">
          <div className="component_leftsidebar_name">{name}</div>
          <div className="component_leftsidebar_phone">{phone}</div>
        </div>
        <Button
          text={'Edit Profile'}
          className="component_leftsidebar_edit_profile"
          btnClassName="component_leftsidebar_btn_edit_profile"
          onClick={onChangeProfile}
        />
      </div>
    </div>
  );
};

const LeftSideBarLogout = ({ onLogout }) => {
  return (
    <div className="component_leftsidebar_logout" onClick={onLogout}>
      <span>Logout</span>
    </div>
  );
};

const LeftSideBar = props => {
  return (
    <LeftSideBarWrapper show={props.show}>
      <div
        className="ant-modal-mask component_leftsidebar_modal_mask"
        onClick={() => props.onHide(false)}
      >
        <div
          className="component_leftsidebar_modal"
          onClick={e => {
            e.stopPropagation();
          }}
        >
          <div className="component_leftsidebar_top_profile">
            <LeftSideBarTitle
              name={props.name}
              phone={props.phone}
              imgProfile={props.imgProfile}
              onHide={props.onHide}
              onChangeProfile={props.onChangeProfile}
            />
          </div>
          <div className="component_leftsidebar_children_container">
            {props.children}
          </div>
          <LeftSideBarLogout onLogout={props.onLogout} />
        </div>
      </div>
    </LeftSideBarWrapper>
  );
};

export default LeftSideBar;
